<!--详情页——明星-->
<template>
  <div class="with_page" style="box-sizing: border-box;margin-top: 32px">
    <div class="page_con">
      <div v-for="(item,index) in specialList" v-if="index>=0" :key="item.id" :id="('a'+index)" class="Oval" >
        <img :src="specialList[index].actorImg" class="con_img">
        <p class="name">{{specialList[index].actorName}}</p>
        <p class="MidName">{{specialList[index].actorName.substring(0,1)}}</p>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
    </div>
  </div>
</template>

<style scoped="Mact">
.with_page {
  position: relative;
  width: 1180px;
  height: 220px;
  margin-top: 42px;
  overflow: hidden;
}
.page_con {
  position: absolute;
  width: 10000px;
  height: 200px;
}
.page_con:after {
  clear: both;
  content: "";
  display: block;
}
.page_con .Oval {
  position: relative;
  width: 114px;
  height: 114px;
  border-radius: 100%;
  float: left;
  margin-right: 45px;
  margin-top: 15px;
  border: 2px solid rgba(255, 255, 255, 0.5);
}
.page_con .Oval:nth-child(1) {
  margin-left: 55px;
}

.page_con .Oval img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
}
.page_con .Oval .name {
  font-family: PingFangSC-Semibold;
  font-size: 20px;
  color: #ececec;
  text-align: center;
  margin-top: 10px;
}
.page_con .Oval .tran {
  border: 4px solid #ffffff;
  transform: scale(1.1);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  margin-left: -4px;
  margin-top: -4px;
}
.MidName {
  position: absolute;
  width: 66px;
  height: 70px;
  font-size: 56px;
  color: #ffffff;
  top: 21px;
  left: 24px;
  text-align: center;
}
/*.page_con .Oval .time{*/
/*box-shadow:2px 4px 10px rgba(0,0,0,0.3);*/
/*}*/
</style>

<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import { HTTPUtil as api } from "../../fetch/api.js";
import c from "../../js/common.js";
import { xha } from "../../js/xha.js";
export default {
  props: ["initData", "jsonUrl"],
  data() {
    return {
      name: "Movie_actorList",
      itemNo: 0,
      isShow: false,
      specialList: [],
      eleId: ""
    };
  },
  computed: {
    ...mapGetters(["behindParams", "isSouth"])
  },
  created() {
    this.specialList = this.initData.data.actorList;
    this.eleId = this.initData.data.actorList[0].actorId; //默认数据第一个为组件内的id
    this.name = "Movie_actorList"; //默认数据第一个为组件名字
    this.title = this.initData.data.assetId; //默认标题
  },
  methods: {
    pageLoad(name){
      // 星红桉埋点
      let SrcPagePath = "";
      let PageName = name;
      let AsssetID = this.initData.data.assetId;
      let Id = localStorage.getItem("position").split('_')[0];
      let TYPE = "";
      xha.pageLoad("详情页演员推荐",SrcPagePath,PageName,AsssetID,Id,TYPE);
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        yh.stop(true);
        let url = this.specialList[this.itemNo].jsonUrl;
        let name = this.specialList[this.itemNo].actorName;
        this.pageLoad(name);
        c.routerSkip(url, "2", "", this.$router);
      }
    },
    back() {
      this.$emit("sendBp", "goBack"); // 将消息发给父组件
    },
    left() {
      if (this.itemNo == 0) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
      if (this.itemNo <= this.specialList.length - 8) {
        $(".page_con").css({
          transition: "all 0.1s",
          transform: "translateX(-" + 175 * this.itemNo + "px)"
        });
      }
    },
    right() {
      if (this.itemNo >= this.specialList.length - 1) return;
      if (this.itemNo >= 6) {
        $(".page_con").css({
          transition: "all 0.1s",
          transform: "translateX(-" + 175 * (this.itemNo - 5) + "px)"
        });
      }
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    up() {
      $(".con_img").removeClass("tran");
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
      this.$emit("eleScroll", this.name, "up");
    },
    down() {
      this.removeCss();
      this.addCss();
    },
    removeCss() {
      if (this.position == 0) {
        $(".con_img").removeClass("tran");
      } else {
        $("#a" + this.itemNo + " img").removeClass("tran");
      }
    },
    addCss() {
      if (this.position == 0) {
        $(".con_img").addClass("tran");
      } else {
        $("#a" + this.itemNo + " img").addClass("tran");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.itemNo = 0;
      $(".page_con").css({
        transition: "all 0.1s",
        transform: "translateX(0px)"
      });
      if (bl) {
        $("#a" + this.itemNo + " img").addClass("tran");
        if (pos == "down") {
          let eleH = this.cEleOffsetTop(".with_page .page_con");
          this.$emit("eleScroll", this.name, pos, eleH);
        }
      } else {
        this.removeCss();
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 200;
      return realHeight;
    }
  },
  watch: {
    initData() {
      this.specialList = this.initData.data.actorList;
      this.eleId = this.initData.data.actorList[0].actorId; //默认数据第一个为组件内的id
    }
  },
  components: {}
};
</script>



