<!--详情页——弹出层-->
<template>
    <div class="yy-confirm" v-show="visible">
      <div class="bg"></div>
      <div class="yy-container" :style="{width: width}">
        <div class="yy-content">
          <div class="yy-content-scroll">
            {{isAlert.msg}}
          </div>
        </div>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
    </div>
</template>
<script type="text/javascript">
export default {
  props: ["isAlert"],
  data() {
    return {
      name: "description",
      isShow: false,
      eleId: "",
      num: 0,
      width: {
        type: String,
        default: "360px"
      },
      visible: false
    };
  },
  methods: {
    showBox() {
      this.visible = true;
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.KeyBack();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      }
    },
    KeyBack() {
      this.visible = false;
      this.isShow = false;
      if (this.isAlert.alert) {
        this.$emit("press", this.isShow, "description", "KeyBack");
        this.num = 0;
        $(".yy-content-scroll")
          .stop(false, true)
          .animate({ top: "-" + this.num * 200 + "px" }, 10);
      } else {
        this.$emit("press", this.isShow, "description", "KeyBack");
      }
    },
    down() {
      let fEleDistance = $(".yy-content-scroll")[0].scrollHeight;
      if (fEleDistance>330) {
        if(this.num>0 ){
         if(parseFloat(fEleDistance /(330*(this.num+1)))>1){
            this.num++;
            $(".yy-content-scroll")
            .stop(false, true)
            .animate({ top: "-" + this.num * 320 + "px" }, 10);
         }
        }else{
          this.num++;
          $(".yy-content-scroll")
            .stop(false, true)
            .animate({ top: "-" + this.num * 320 + "px" }, 10);
        }
        
      } else {
        this.num = item - 1;
      }
    },
    up() {
      this.num--;
      if (this.num >= 0) {
        $(".yy-content-scroll")
          .stop(false, true)
          .animate({ top: "-" + this.num * 320 + "px" }, 10);
      } else {
        this.num = 0;
      }
    },
    updateStast(pos, down, poss) {
      this.visible = true;
      this.isShow = true;
    }
  },
  mounted() {},
  components: {},
  watch: {
    isAlert() {
      if (this.isAlert.alert) {
        this.visible = true;
        this.isShow = true;
      } else {
        this.visible = false;
      }
    }
  }
};
</script>

<style type="text/css" scoped="confirm">
.yy-content-scroll {
  position: absolute;
  text-align: left;
  width: 605px;
  text-indent: 39px;
  left: 0;
  top: 0;
  padding: 50px 45px 0px 45px;
  background: #37506c;
}
.bounce-enter-active {
  -webkit-transform: scale(2);
  -moz-transform: scale(2);
  -ms-transform: scale(2);
  transform: scale(2);
  opacity: 0;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}
.bounce-leave-active {
  -webkit-transform: scale(2);
  -moz-transform: scale(2);
  -ms-transform: scale(2);
  transform: scale(2);
  opacity: 0;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}
.yy-confirm {
  position: fixed;
  top: 0;
  left: 0;
  width: 1280px;
  height: 720px;
  z-index: 999;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 14px;
}
.yy-confirm .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.3;
  z-index: 99999;
}
.yy-confirm .yy-container {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 1280px;
  z-index: 9999999999999;
}
.yy-confirm .yy-container .yy-content {
  border-radius: 14px 14px 14px 14px;
  border: 2px solid white;
  color: white;
  opacity: 0.8;
  height: 343px;
  width: 695px;
  line-height: 40px;
  font-size: 20px;
  font-family: "proxima nova", "Roboto", "helvetica", Arial, sans-serif;
  background: #37506c;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
}
</style>



