<!--详情页——专题标签-->
<template>
  <div class="with_page" style="box-sizing: border-box;">
    <div class="special_con">
      <div v-for="(item,index) in specialList" v-if="index>=0&& index<2" :key="item.id" :id="('acc'+index)" class="Oval" >
        <!--<img v-if="isIndex" :src="specialList[index].elementImg" class="con_img">-->
        <lazy-image v-if="isIndex"
          :src='specialList[index].elementImg'
          :placeholder='placeholder'
          :initScore = 'initScore'
          class="con_img"
        />
        <lazy-image v-if="!isIndex"
                    :src='specialList[index].specialImg'
                    :placeholder='placeholder'
                    :initScore = 'initScore'
                    class="con_img"
        />
        <!--<lazy-image-->
                    <!--:src='specialList[index].bulgeImg'-->
                    <!--:placeholder='placeholder'-->
                    <!--:initScore = 'initScore'-->
                    <!--:score = "item.score"-->
                    <!--:title = "item.assetName"-->
                    <!--class="con_img1"-->
        <!--/>-->
        <!--<img v-if="!isIndex" :src="specialList[index].specialImg" class="con_img">-->
        <img :src="specialList[index].bulgeImg" class="con_img1">
        <p class="name">{{specialList[index].specialName}}</p>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
    </div>
  </div>
</template>

<style scoped="special">
.with_page {
  width: 1280px;
  height: 295px;
  margin-top: 20px;
}
.special_con {
  width: 1280px;
}
.special_con:after {
  clear: both;
  content: "";
  display: block;
}
.special_con .Oval {
  position: relative;
  background: #ffffff;
  width: 540px;
  height: 240px;
  float: left;
  margin-right: 45px;
  margin-top: 15px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 5px;
}
.special_con .Oval img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 5px;
}
.special_con .Oval .cha {
  transform: scale(1.04);
  position: absolute;
  left: 0;
  top: -1px;
}
.special_con .Oval .name {
  font-family: PingFangSC-Semibold;
  font-size: 22px;
  font-weight: bold;
  color: #ffffff;
  margin-top: 10px;
  position: absolute;
  left: 10px;
  bottom: 10px;
}
.special_con .Oval.tran {
  z-index: 99999;
  border: 3px solid #ffffff;
  transform: scale(1.1);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  margin-left: -4px;
}
</style>

<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";

import { HTTPUtil as api } from "../../fetch/api.js";
export default {
  props: ["initData", "jsonUrl"],
  data() {
    return {
      name: "special",
      itemNo: 0,
      isShow: false,
      specialList: [],
      isIndex: false,
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      eleId: ""
    };
  },
  computed: {
    ...mapGetters(["behindParams", "isSouth"])
  },
  created() {
    if (this.initData.data == undefined) {
      // 首页数据
      this.specialList = this.initData.elementList;
      this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
      this.name = this.initData.layout; //默认数据第一个为组件名字
      this.title = this.initData.specialName; //默认标题
      this.isIndex = true;
    } else {
      // 详情页数据
      this.specialList = this.initData.data.specialList;
      this.eleId = this.initData.data.specialList[0].specialId; //默认数据第一个为组件内的id
      this.name = "special"; //默认数据第一个为组件名字
      this.title = this.initData.data.specialList.specialName; //默认标题
    }
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        let url = this.specialList[this.itemNo].jsonUrl;
        if(this.specialList[this.itemNo].layout=="Mix_Subject"){
          this.$router.push({ name: "AllSpecial", params: { jsonUrl: url } });
        }else{
          this.$router.push({ name: "Special", params: { jsonUrl: url } });
        }
      }
    },
    back() {
      this.$emit("sendBp", "goBack"); // 将消息发给父组件
    },
    addCss() {
      $("#acc" + this.itemNo + " .con_img1").addClass("cha");
      $("#acc" + this.itemNo).addClass("tran");
    },
    removeCss() {
      $("#acc" + this.itemNo + " .con_img1").removeClass("cha");
      $("#acc" + this.itemNo).removeClass("tran");
    },
    right() {
      if (this.itemNo >= 1) return;
      this.removeCss();
      if (this.itemNo < this.specialList.length - 1) {
        this.itemNo++;
      } else {
        this.itemNo = this.specialList.length - 1;
      }
      this.addCss();
    },
    left() {
      this.removeCss();
      if (this.itemNo > 0) {
        this.itemNo--;
      } else {
        this.itemNo = 0;
      }
      this.addCss();
    },
    down() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "down");
    },
    up() {
      this.removeCss();
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
    },
    updateStast(bl, pos, isEnd) {
      this.itemNo = 0;
      this.isShow = true;
      this.addCss();
      if (bl) {
        if (pos == "down") {
          this.addCss();
          let eleH = this.cEleOffsetTop(".con_img");
          this.$emit("eleScroll", this.name, pos, eleH);
        }
      } else {
        this.removeCss();
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 350;
      return realHeight;
    }
  },
  watch: {
    initData() {
      if (this.initData.data == undefined) {
        // 首页数据
        this.specialList = this.initData.elementList;
        this.eleId = this.initData.specialId; //默认数据第一个为组件内的id
        this.name = this.initData.layout; //默认数据第一个为组件名字
        this.title = this.initData.specialName; //默认标题
        this.isIndex = true;
      } else {
        // 详情页数据
        this.specialList = this.initData.data.specialList;
        this.eleId = this.initData.data.specialList[0].specialId; //默认数据第一个为组件内的id
        this.name = "special"; //默认数据第一个为组件名字
        this.title = this.initData.data.specialList.specialName; //默认标题
      }
    }
  },
  components: {}
};
</script>



