<!--详情页——双层选集层-->
<template>
  <div style="box-sizing: border-box;margin-bottom: 40px;margin-top:50px" class="pt" >
    <div class="ru">
      <div class="ruG"></div>
    </div>
      <div class="markContent" v-show="showVip">
        <div class="cornerMark" id="cornerMark">
          <template  v-for="(item,index) in top"  v-if="index>=0">
            <span :key="item.id" class="corner_mark"><i v-show="isAuth?false:item.fee==0?true:false">VIP</i></span>
          </template>
        </div>
      </div>
    <div class="ption-top border-ption" id="pt2">
      <div class="ptionL2"><span v-show="isShowPlay">{{isAuth?isFree:top[num].fee==0?noPay:isFree}}</span></div>
      <div class="ption">
        <template v-for="(item,index) in top"  v-if="index>=0">
             <p :key="item.id" :id="('c'+index)" class="text_num">
                {{top[index].episode}}
            </p>
        </template>
      </div>
    </div>
    <div class="ption-top " id="pt1" style="margin-top: 20px">
      <div class="ptionL1"></div>
      <div class="ptionx">
        <p v-for="(item,index) in this.a" :key="item.id" v-if="index>=0" :id="('a'+index)" class="text_num">
          {{item-1}}1-{{item}}0
        </p>
        <p class="text_num" :id="('a'+this.a)" v-if="this.acc>=(this.a*10)+1">{{(this.a*10)+1}}-{{this.acc}}</p>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>

</template>
<style>
.ru {
  position: absolute;
  top: 406px;
  width: 80px;
  height: 5px;
  background: white;
  margin-left: 510px;
  border-radius: 5px;
  border-top: 1px solid white;
}
.ruG {
  width: 0;
  height: 0;
  border: 5px solid red;
  border-color: #f6f6f7 transparent transparent transparent;
  margin-left: 34px;
  margin-top: 5px;
}
.ption-top.border-ption {
  background: rgba(255, 255, 255, 0.5);
}
.ption-top {
  width: 1167px;
  height: 44px;
  position: relative;
  overflow: hidden;
  margin-top: 25px;
  border-radius: 5px;
  background: -webkit-linear-gradient(
    to left,
    rgba(255, 255, 255, 0.01) 0%,
    rgba(255, 255, 255, 0.5) 50%,
    rgba(255, 255, 255, 0.01) 100%
  );
  background: linear-gradient(
    to left,
    rgba(255, 255, 255, 0.01) 0%,
    rgba(255, 255, 255, 0.5) 50%,
    rgba(255, 255, 255, 0.01) 100%
  );
}
.ption {
  width: 100000%;
  height: 46px;
  overflow: hidden;
  position: absolute;
  left: 500px;
  top: 0;
  transition: all 0.01s;
}
.ptionL {
  text-align: center;
  opacity: 0.66;
  background: white;
  width: 100px;
  height: 46px;
  position: absolute;
  left: 500px;
  top: 0;
}
.ption .text_num {
  width: 100px;
  height: 44px;
  font-family: PingFangSC-Regular;
  font-size: 18px;
  text-align: center;
  float: left;
  line-height: 46px;
}
.ptionx {
  width: 1000%;
  height: 46px;
  overflow: hidden;
  position: absolute;
  left: 500px;
  top: 0;
  transition: all 0.7s;
}
.ptionL1 {
  text-align: center;
  background: rgba(255, 255, 255, 0.3);
  width: 100px;
  height: 44px;
  position: absolute;
  left: 500px;
  top: 0;
  box-sizing: border-box;
  border-radius: 15px;
}
.ptionL2 {
  text-align: center;
  background: rgba(255, 255, 255, 0.3);
  width: 100px;
  height: 44px;
  position: absolute;
  left: 500px;
  top: 0;
  box-sizing: border-box;
  border-radius: 15px;
  opacity: 1;
  line-height: 44px;
  padding-left: 24px;
}
.ptionL2 {
  opacity: 1;
}
.ptionL2.hover {
  background-color: #0098bc !important;
  background: url("../../assets/play.png") no-repeat center center;
  background-position-x: 26px;
  z-index: 95 !important;
}
.backchange {
  background: #0098bc;
  border: solid 2px white;
  border-radius: 14px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.75);
}

.ptionx .text_num {
  width: 100px;
  height: 46px;
  font-family: PingFangSC-Regular;
  font-size: 18px;
  text-align: center;
  float: left;
  line-height: 46px;
}
.corner_mark {
  position: relative;
  width: 100px;
  height: 25px;
  text-align: center;
  z-index: 100;
  font-style: normal;
  line-height: 25px;
}
.corner_mark i {
  border-radius: 20px;
  position: absolute;
  background-color: #f4bf42;
  color: black;
  width: 50px;
  height: 25px;
  font-style: normal;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.markContent {
  width: 1167px;
  height: 44px;
  position: absolute;
  overflow: hidden;
  margin-top: -24px;
  border-radius: 5px;
  z-index: 99;
}
#cornerMark {
  position: relative;
  display: flex;
  width: 100000%;
  height: 46px;
  overflow: hidden;
  left: 500px;
  top: 0;
  overflow: hidden;
  margin-top: 10px;
  border-radius: 5px;
}
</style>
<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import c from "../../js/common.js";

export default {
  props: ["initData", "jsonUrl", "hide", "isAuth", "isPreView", "assetInfo","productId","FilmType"],
  name:"Selection",
  data() {
    return {
      assId: "",
      name: "ption",
      op: 1,
      itemNo: 0,
      eleId: "",
      num: 0,
      a: 0,
      isEnd: false,
      showl: true,
      isShow: false,
      playerTimer: null,
      isFree:'播放',
      noPay:'付费', 
      isShowPlay:false, // 是否显示播放按钮
      top: [
        {
          episode: ""
        }
      ],
      acc:0,
      showVip:false,
      EnterType:localStorage.getItem('EnterType') || 1,
    };
  },
  computed: {
    ...mapGetters([
      "category",
      "behindParams",
      "isHTTPS",
      "navpos",
      "isSouth",
      "episode"
    ])
  },
  created() {
    this.top = this.initData.data.itemList;
    this.acc = this.initData.data.itemList.length;
    this.numm = parseFloat(this.acc);
    this.a = Math.floor(this.numm / 10);
    this.eleId =  this.top[0].itemId;
    this.$nextTick(function() {
      console.log("VueX 存储历史集数：" + this.episode);
      try {
        if (this.episode !== undefined) {
          console.log("历史集数" + this.episode);
          this.num = this.episode;
          $(".ption").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          $(".cornerMark").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          this.itemNo = Math.floor(this.num / 10);
          $(".ptionx").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.itemNo + "px)"
          });
        }
      } catch (error) {
        console.log(error);
      }
    });
  },
  updated() {
    this.top = this.initData.data.itemList;
    this.acc = this.initData.data.itemList.length;
    this.numm = parseFloat(this.acc);
    this.a = Math.floor(this.numm / 10);
    this.eleId =  this.top[0].itemId;
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        this.KeyEnter();
      }
    },
    back() {
      this.$emit("sendBp", "goBack"); // 将消息发给父组件
    },
    right() {
      if (this.op == 0) {
        if (this.acc > this.a * 10) {
          if (this.itemNo >= this.a) return;
        } else {
          if (this.itemNo >= this.a - 1) return;
        }
        this.itemNo += 1;
        this.num = 10 * this.itemNo;
        $(".ptionx").css({
          transition: "all 0.3s",
          transform: "translateX(-" + 100 * this.itemNo + "px)"
        });
        $(".ption").css({
          transition: "all 0.3s",
          transform: "translateX(-" + 100 * this.num + "px)"
        });
        $(".cornerMark").css({
          transition: "all 0.3s",
          transform: "translateX(-" + 100 * this.num + "px)"
        });
      }
      if (this.op == 1) {
        $(".ptionL2").addClass("hover");
        $(".ptionL1").removeClass("backchange");
        if (this.num >= this.top.length - 1) return;
        this.num++;
        $(".ption").css({
          transition: "all 0.3s",
          transform: "translateX(-" + 100 * this.num + "px)"
        });
        $(".cornerMark").css({
          transition: "all 0.3s",
          transform: "translateX(-" + 100 * this.num + "px)"
        });
      }
      for (let i = 0; i < this.a + 1; i++) {
        if (this.num == i * 10) {
          this.itemNo = i;
          this.trans();
        }
      }
    },
    trans() {
      $(".ptionx").css({
        transition: "all 0.3s",
        transform: "translateX(-" + 100 * this.itemNo + "px)"
      });
    },
    left() {
      if (this.op == 1) {
        if (this.num > 0) {
          this.num--;
          $(".ption").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          $(".cornerMark").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          for (let i = 0; i < this.a + 1; i++) {
            if (this.num == i * 10 - 1) {
              this.itemNo = i - 1;
              this.trans();
            }
          }
        }
      }
      if (this.op == 0) {
        if (this.itemNo > 0) {
          this.itemNo--;
          this.num = 10 * this.itemNo;
          $(".ptionx").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.itemNo + "px)"
          });
          $(".ption").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          $(".cornerMark").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
        } else {
          this.itemNo = 0;
          this.num = 0;
          $(".ptionx").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.itemNo + "px)"
          });
          $(".ption").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          $(".cornerMark").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
        }
      }
    },
    down() {
      this.isShowPlay = false;
      $(".ptionL1").addClass("backchange");
      $(".ptionL2").removeClass("hover");
      $("#pt1").addClass("border-ption");
      $("#pt2").removeClass("border-ption");
      this.op -= 1;
      if (this.op < 0 && this.isEnd == false) {
        this.isShow = false;
        this.op = 0;
        $("#pt1").removeClass("border-ption");
        $("#pt2").removeClass("border-ption");
        $(".ptionL1").removeClass("backchange");
        $(".ptionL2").removeClass("hover");
        this.$emit("pressDown", this.isShow, this.eleId, "down");
        // let eleH = this.cEleOffsetTop('.pt');
        // this.$emit('eleScroll',"ption",'up',eleH);
      }
    },
    up() {
      this.isShowPlay = true;
      $(".ptionL2").addClass("hover");
      $(".ptionL1").removeClass("backchange");
      $("#pt2").addClass("border-ption");
      $("#pt1").removeClass("border-ption");
      this.op += 1;
      if (this.op > 1) {
        this.isShow = false;
        this.isShowPlay = false;
        this.op = 1;
        $("#pt1").removeClass("border-ption");
        $("#pt2").removeClass("border-ption");
        $(".ptionL1").removeClass("backchange");
        $(".ptionL2").removeClass("hover");
        this.$emit("pressDown", this.isShow, this.eleId, "up", "100");
      }
    },
    updateStast(bl, pos, isEnd) {
      this.isShow = bl;
      this.isEnd = isEnd;
      if (pos == "KeyEnter") {
        this.isShowPlay = true;
        this.op = 1;
        $("#pt2").addClass("border-ption");
        $(".ptionL2").addClass("hover");
      }else if(pos == "orderBack"){ // 选集支付跳转取消
          this.updateEisode(); // 更新集数
      }
      if (bl) {
        if (this.op == 1) {
          this.isShowPlay = true;
          $("#pt2").addClass("border-ption");
          $(".ptionL2").addClass("hover");
        } else {
          $("#pt1").addClass("border-ption");
          $(".ptionL1").addClass("backchange");
        }
      } else {
        this.isShowPlay = false;
        $(".ptionL2").removeClass("hover");
        this.removeCss();
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 450;
      return realHeight;
    },
    play(bud) {
      //        判断播放方法
      let tempUrl = this.jsonUrl;
      let obj = {};
      try {
        let id = this.initData.data.itemList[this.num].itemId;
        let pageId = this.initData.data.assetId;
        let traceId = "|" + this.catId;
        let traceType = "|" + this.catCode;
        bi.categoryClick(this.catId, id, 5, 31, pageId, traceId, traceType);
      } catch (error) {
        console.log("埋点异常");
      }
      this.$store.commit("GET_EPISODE", this.num); //  切换集数更新vuex存储集数
      obj = {
        index: this.num,
        startTime: 0,
        jsonUrl: tempUrl,
        buder: bud,
        preView: !this.isAuth,
        callback: "preViewCallback"
      };
      console.log("detail:" + JSON.stringify(obj));
      yh.play(JSON.stringify(obj));
    },
    removeCss() {
      $("#b" + this.itNum).removeClass("change");
      $(".advert").removeClass("change");
    },
    KeyEnter() {
      if (this.op == 1) {
        if (this.isSouth) {
          let xhalogs = {};
          try {
              let columnId = this.category[this.navpos].catId;
              let columnName = this.category[this.navpos].catName;
              let catCode = this.category[this.navpos].catCode;
              const assetId = this.assetInfo.contentCode;
              const filmName = this.initData.data.assetName;
              const Episodes = this.num+1;
              const vodEpisodes = this.initData.data.itemList.length;
              const state ="0";
              let productId = this.productId;
              let SPID = this.initData.data.providerMemo.split(',')[0];
           
              if(this.EnterType == 0){
                columnName = '搜索';
                columnId = '00';
                catCode = 'search'
              }
              xhalogs = c.ynPlayLog(columnId,columnName,assetId,filmName,Episodes,vodEpisodes,this.EnterType,this.FilmType,state,productId,SPID);
            } catch (error) {
              console.log('豫南华为播放器传递采集信息异常')
            }
          if (this.isAuth) {
            let url = this.initData.data.itemList[this.num].vodList[0].playUrl;
            c.getSouthHistoryData(
              this.assetInfo.contentName,
              this.assetInfo.contentCode,
              this.assetInfo.assetImg,
              this.jsonUrl,
              this.initData.data.assetType
            );
           yh.HWplay(url,false,xhalogs);
           
          } else {
            if(this.initData.data.itemList[this.num].fee === "1" || this.initData.data.itemList[this.num].fee === 1){ // 电视剧前三集免费
              let url = this.initData.data.itemList[this.num].vodList[0].playUrl;
              c.getSouthHistoryData(
                this.assetInfo.contentName,
                this.assetInfo.contentCode,
                this.assetInfo.assetImg,
                this.jsonUrl,
                this.initData.data.assetType
              );
              yh.HWplay(url,false,xhalogs);
              
            }else{ // 鉴权失败需要支付
              this.removeCss();
              this.isShow = false;
              this.$emit("controllMeg", false, "order", "vipOrder", "selection");
            }
           
          }
        } else {
          if(this.isAuth){
            this.play(false);
          }else{
              let fee = this.top[this.num].fee;
              if(fee==1||fee=="1"){
                  console.log("第"+this.num+"免费!");
                  this.play(false);
              }else{
                console.log("第"+this.num+"付费!");
                 yh.stop(false);
                this.removeCss();
                this.isShow = false;
                this.$emit("controllMeg", false, "order", "vipOrder", "selection");
              }
             
          }
        }
      }
    },updateEisode(){
       console.log("弹窗返回需要更新集数" + this.episode);
      try {
        if (this.episode !== undefined) {
          this.num = this.episode;
          $(".ption").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          $(".cornerMark").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          this.itemNo = Math.floor(this.num / 10);
          $(".ptionx").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.itemNo + "px)"
          });
        }
      } catch (error) {
        console.log(error);
      }
    },IsShowVip(isNeed){
      this.showVip = isNeed;
    }
  },
  watch: {
    initData() {
      this.top = this.initData.data.itemList;
      this.acc = this.initData.data.itemList.length;
      this.eleId =  this.top[0].itemId;
      this.EnterType = localStorage.getItem('EnterType') || 1;
      this.numm = parseFloat(this.acc);
      this.a = Math.ceil(this.numm / 10);
      this.op = 1;
      this.itemNo = 0;
      this.num = 0;
      $(".ptionx").css({
        transition: "all 0.3s",
        transform: "translateX(-" + 100 * this.itemNo + "px)"
      });
      $(".ption").css({
        transition: "all 0.3s",
        transform: "translateX(-" + 100 * this.num + "px)"
      });
      $(".cornerMark").css({
        transition: "all 0.3s",
        transform: "translateX(-" + 100 * this.num + "px)"
      });
    },
    episode() {
      console.log(
        "退出全屏集数有更新,同步更新选集条,当前选集： " + this.episode
      );
      try {
        if (this.episode !== undefined) {
          this.num = this.episode;
          $(".ption").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          $(".cornerMark").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.num + "px)"
          });
          this.itemNo = Math.floor(this.num / 10);
          $(".ptionx").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 100 * this.itemNo + "px)"
          });
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>



