var bp = {};

var _gaq = _gaq || [];

const isSouth = localStorage.getItem("isSouth");

_gaq.push(['sys_v', yh.sys_v]); // 系统版本
_gaq.push(['model', yh.model]); // 用户设备版本
_gaq.push(['soft_v', yh.soft_v]); // 用户软件版本
_gaq.push(['device_id', yh.device_id]); // 设备号
_gaq.push(['site_id', yh.siteId]); // 站点id
_gaq.push(['user_id', yh.userId]); // 用户id
if (isSouth){
    _gaq.push(['log_type', '1-1']); // 日志类型
}else{
    _gaq.push(['log_type', '1-2']); // 日志类型
}
_gaq.push(['create_time', c.formatDate(new Date().getTime())]); // 事件创建时间
_gaq.push(['operator_id', yh.operator_id]); // 运营商ID（广电：1 电信：2 移动：3 联调：4）
_gaq.push(['terrace_id', yh.terrace_id]); // 平台ID(中兴：1 华为：2)
_gaq.push(['content_id', yh.content_id]); // (牌照方厂家：央视国际1 杭州华数2 百事通3 南方传媒4 湖南卫视5 银河电视6 国广东方7 爱上北京8)
_gaq.push(['brand', yh.brand]); // 机顶盒品牌
_gaq.push(['mode', yh.mode]); // 机顶盒型号
_gaq.push(['apk_version', yh.apk_version]); // apk版本
_gaq.push(['reserve_group', yh.reserve_group]); // 用户分组
_gaq.push(['collect_type', yh.collect_type]); // 采集方式
_gaq.push(['v', '3']);



// 应用启动
bp.openApp = function(){
    _gaq.push(['event',102]);
    _gaq.push(['start_type', 1]); // 启动应用渠道，1直接打开，2推荐位启动
    combineArgs();
}

// 关闭应用
bp.closeApp = function (pageId, keepTime){
    const stayTime = new Date().getTime() - keepTime;
    console.log("应用停留时间:"+stayTime+" ms");
    _gaq.push(['event', 103]);
    _gaq.push(['trace_type', 1]);
    _gaq.push(['page_type', 1]);
    _gaq.push(['trace_id', pageId]);
    _gaq.push(['page_id', pageId]);
    _gaq.push(['time', stayTime]);
    combineArgs();
}

// 访问页面（加载时长）
bp.loadPage = function (traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, special_type, keepTime){
    if (special_type!=""){
        _gaq.push(['special_type', special_type]); // 只有专题类型才可以传
    }
    _gaq.push(['event', 104]);
    _gaq.push(['time', keepTime]);
    _gaq.push(['trace_id', traceId]);
    _gaq.push(['trace_type', traceType]);
    _gaq.push(['page_type', pageType]);
    _gaq.push(['page_id', pageId]);
    _gaq.push(['parent_page_type', parent_page_type]);
    _gaq.push(['parent_page_id', parent_page_id]);
    combineArgs();
}

// 离开页面（页面停留时间）
bp.closPage = function(traceId, traceType, pageType, pageId, special_type, oldTime) {
    if (special_type != "") {
        _gaq.push(['special_type', special_type]);
    }
    _gaq.push(['event', 105]);
    _gaq.push(['time', oldTime]);
    _gaq.push(['trace_id', traceId]);
    _gaq.push(['trace_type', traceType]);
    _gaq.push(['page_type', pageType]);
    _gaq.push(['page_id', pageId]);
    combineArgs();
}

