<template>
    <div class="wrap">
        <div class="box">
            <div class="innerBOX">
                <div class="scrollBox">
                    <div class="title">
                    <!-- 撼动人心的精彩 -->
                    </div>
                    <div class="temp">
                            <div  v-for="(item,index) in dataList" :key="index" :class="itemNo == index?'innerItemBox active':'innerItemBox'">
                              <p :class="{'price_origin':true,'isVisibility':filterQS(item.productCode)}"><s>原价<span>{{qsPrice(item.productCode)}}元</span></s></p>
                               <p class="price_now"> {{item.productName}}<span style="font-size:30px;">{{discountPrice(item.productCode,item.price)}}</span>元</p>
                            </div>
                    </div>
                </div>
                <div class="scrollBar">
                    <div class="bar"></div>
                </div>
            </div>    
        </div>
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ></keyDo>
    </div>
</template>

<style scoped>
.wrap{position: absolute;top: 0px;}
.scrollBar{
    display: none;
    position: absolute;
    width: 7px;
    height: 415px;
    background-color: #cccccc;
    top: 105px;
    right: 74px;
    border-radius:5px;
}
.bar{
    width: 11px;
    height: 83px;
    background-color: #ffffff;
    position: absolute;
    left: -2px;
    border-radius: 8px;

}
.box{
    background-color: rgba(0, 0, 0, .67);
    width: 1280px;
    height: 720px;
    position: relative;
    z-index: 99;  
}
.innerBOX{
    width: 886px;
    height: 542px;
    background: rgba(55, 80, 108, 1);
    border:1px solid #A7A7A7;
    box-shadow: 0 30px 40px rgba(0, 0, 0, 0.37);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 25px;
    box-sizing: border-box;
    padding-left: 123px;
    padding-top: 54px;
    padding-right: 122px;
    overflow: hidden;
}
.scrollBox{
    position: absolute;
}
.innerBOX .title{
    font-size: 32px;
    font-family: PingFang HK, Regular;
    color: rgba(248, 212, 61, 1);
}
.innerBOX .temp::before{
    clear: both;
    display: block;
    content: '';
}
.innerBOX .temp{
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 667px;  
}
.innerItemBox{
    margin-right: 40px;
    margin-top: 60px;
    width: 180px;
    height: 96px;
    border-radius: 15px;
    background: rgba(147, 155, 162, 1);
    border:1px solid rgba(226, 226, 226, 1);
    color:rgba(255, 255, 255, 1);
    font-size: 18px;
    /* text-align: center;
    line-height: 96px; */
}
.isVisibility{
    visibility: visible !important;
}
.price_origin{
    position: relative;
    width: 100%;
    height: 33px;
    display: inline-block;
    text-align: center;
    line-height: 33px;
    visibility: hidden;
    font-size: 16px;
}
.price_now{
    position: relative;
    width: 100%;
    height: 50px;
    display: inline-block;
    text-align: center;
    line-height: 30px;
}
.active{
    background-color: rgba(0, 152, 188, 1) !important;
    border:3px solid rgba(255, 255, 255, 1) !important;
    box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4) !important;
    margin: -2px !important;
    margin-top: 58px !important;
    margin-right:38px !important;
}
.hover{
    background-color: rgba(0, 152, 188, 1) !important;
    border:3px solid rgba(255, 255, 255, 1) !important;
    box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.4) !important;
    margin: -2px !important;
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 58px !important;  
}
</style>

<script>
import { HTTPUtil as api } from "../../fetch/api.js";
import {client as yh} from '../../js/client.js'
import {Authentication as auth} from '../../js/auth.js'
import {xha} from '../../js/xha.js'
import {bp as bi} from '../../js/ga.js'
import Toast from '../../tool/toast/toast.js'
import c from '../../js/common.js'
export default {
    props:['orders','assetInfo'],
    name:'order',
    data(){
        return {
            dataListLength:0,
            step:0,
            isShow:true,
            itemNo:0,
            row:1,
            dataList:[
              
            ],
            discountList:[{productCode:"20070035",productName:"优酷包月",price:"25.00"},{productCode:"20070034",productName:"优酷包季",price:"70.00"}],
            filterList:[{productCode:"810098000012",productName:"优品影视包月"},{productCode:"810098000002",productName:"优品影视包季"},{productCode:"810098000001",productName:"优品影视包年"}], // 非优品影视的资产需要过滤优品包
        };
    },
    created(){
        window.getPayStatus = this.getPayStatus;// 客户端回调支付状态
        window.getProInfo = this.getProInfo;// 客户端获取鉴权产品包
        let orderList = this.orders;
        // console.log(orderList);
        if(orderList==null||orderList==undefined){
            Toast({ message: "获取可订购产品包列表失败，请退出重试！", position: "bottom" });
            
            return;
        }else if(orderList.code==1004){
             Toast({ message: "用户不存在或非法，请联系客服解决。", position: "bottom" });
             return;
        }else{
            if(this.assetInfo.assetSp!=="YPYS"){ // 非优品影视需要屏蔽优品影视资产
                let tempArr = [];
                for (let index = 0; index < orderList.length; index++) {
                    const element = orderList[index];
                    let  flag = true;
                    for (let item = 0; item < this.filterList.length; item++) {
                        const fliterP = this.filterList[item];
                        // console.log(element.productCode+"^^^^^^^"+fliterP.productCode);
                        if(element.productCode==fliterP.productCode){
                            // console.log("数组去重："+element.productCode);
                            flag = false;
                            break;
                        }
                    }
                    if(flag){
                        tempArr.push(element);
                    }
                }
                orderList = tempArr;
            }
            try{
                    this.dataList = orderList.sort(function(a,b){
                       return  parseInt(a.cycleUnit)-parseInt(b.cycleUnit)
                    });
            }catch(e){
                    this.dataList = orderList;
                    console.log("可订购列表排序异常");
            }
           
        }
        this.init()
    },
    updated() {
        $('.loading').hide();
    },
    methods: {
        filterQS(id){
            // return false;
            for (let index = 0; index < this.discountList.length; index++) {
                const element = this.discountList[index];
                if(element.productCode==id){
                    console.log("优酷限时优惠"+element.productName);
                    return false;
                }
                
            }
        },discountPrice(code,price){
            // 产品打折
            // if(code == "20070035"){
            //     return "9.9";
            // }else if(code == "20070034"){
            //     return "27";
            // }else{
                return (price/100);
            // }

        },qsPrice(id){
            for (let index = 0; index < this.discountList.length; index++) {
                const element = this.discountList[index];
                if(element.productCode==id){
                    console.log("求索原价"+element.price);
                    return element.price;
                }
                
            }
        },
        init(){
            $('#a0b0').addClass('active');
            this.dataListLength = this.dataList.length;
           if(this.dataListLength>6){
               $('.scrollBar').css('display','block')
           }
           this.step = Math.floor(415/Math.ceil(this.dataListLength/3));
        },
        keyCode(params) {
            if (params == "down") {
                this.down();
            } else if (params == "up") {
                this.up();
            } else if (params == "left") {
                this.left();
            } else if (params == "right") {
                this.right();
            } else if (params == "KeyEnter") {
                $('.openImage').hide();
                $('.loading').show();
                const product = this.dataList[this.itemNo];
                // this.xhaCaiji(product,"0"); 将订购采集改为页面采集
                xha.pageLoad("点播详情页支付弹窗","",this.assetInfo.contentName,this.assetInfo.contentCode,"0","0");
                this.biCaiji(product,"0");
                this.reqProductInfo(product.productCode);
            }else if(params == "KeyBack"){
                this.isShow = false;
                $('.openImage').hide();
                $('.loading').hide();
                this.$emit('controllMeg',false,'orderBack','orderBack',false);
            }
        },
        left(){
            if(this.itemNo == 0)return;
            this.itemNo--;   
            if(this.itemNo == 1 || this.itemNo == 4 || this.itemNo == 7 || this.itemNo == 10 || this.itemNo == 13){
                $('.temp .innerItemBox').eq(this.itemNo).removeClass('active')
               $('.temp .innerItemBox').eq(this.itemNo).addClass('hover')
            }
        },
        right(){
            if(this.itemNo==(this.dataList.length-1))return;
            if(this.itemNo == 2 || this.itemNo == 5 || this.itemNo == 8 || this.itemNo == 11 || this.itemNo == 14)return;
            this.itemNo++; 
        },
        down(){
            if(Math.ceil(this.dataListLength/3) == this.row)return;
            if(this.itemNo+3>this.dataListLength-1)return;
            this.row++;
             this.itemNo+=3; 
            if(this.row>2){
               $(".scrollBox").stop(false, true).animate({ top: -((this.row-2)*100)+'px' }, 100);  
            }
             $(".scrollBar .bar").stop(false, true).animate({ top: (this.step*(this.row)-83)+'px' }, 100);
            
        },
        up(){
             if(this.row ==1)return;
             this.row--;
             this.itemNo-=3; 
             if(this.row == 2){
                     $(".scrollBox").stop(false, true).animate({ top: '54px' }, 100);  
             }
             if(this.row == 1){
                $(".scrollBar .bar").stop(false, true).animate({ top:'0px' }, 100);  
                return;
             }
              $(".scrollBar .bar").stop(false, true).animate({ top: (this.step*(this.row+1)-83)+'px' }, 100);
        },
        scrollEvnd(){
            $(".scrollBar .bar").stop(false, true).animate({ top: +'px' }, 100);
        },
        reqProductInfo(productCode){ // 获取产品包订购信息
           let contentCode = this.assetInfo.contentCode;
           let contentName = this.assetInfo.contentName;
        //    let params = {};
        //     params.url = "/api/Order";
        //     params.type = "post";
        //     params.data = JSON.parse(auth.OrderParams(productCode, contentCode, contentName));
        //     api.ajax(params).then((jsonData)=>{
        //         this.getProInfo(jsonData);
        //     })
       
           yh.HttpPost(auth.OrderUrl,auth.OrderParams(productCode, contentCode, contentName),"getProInfo");
        },
        getProInfo(data){ // 获取BMS产品加密信息
                $('.loading').hide();
                console.log("BMS加密:"+JSON.stringify(data));
                if(data.code===200||data.code==="200"){
                    if(data.result.resultCode==="0000"){
                            //  let ob = {"orderUrl":"http://171.14.99.98:7001/hnitv-api/order","providerId":"17102007","orderInfo":"0wEuliwk7P2GSoxicecNy98zOHxR3ypei8VIQYjeEX5LK/vFEFuYQBXWzONgP633hQnOg4aqILfy2EW+ZX9MEhEZ8PhXrrpxCyMafHIkktLmIT1Ff45Dz4GR3JFxDvkNZQiorL3V0xbIieQf6SbCUSdnCEC6VwAHS5rC/Ttjcgo950ryt/xqyw==","returnUrl":api.paymentUrl(),"imageUrl":"http://dxpay.daxiangjia.com:10371","contentId":"379959","contentName":"西游记女儿国","ip":"1.198.215.7","mac":"000E5ED18191","deviceType":"hd","userGroup":"","deviceModel":"","deviceSerialNo":"","notifyUrl":"http://dxpay.daxiangjia.com:10371/baaa/syncOrder"};
                               let ob = {};
                                ob.orderUrl= auth.fufuOrderUrl;
                                ob.providerId= data.result.providerId;
                                ob.orderInfo = data.result.orderInfo;
                                ob.returnUrl = api.paymentUrl();
                                ob.imageUrl = data.result.imageUrl;
                                ob.contentId = this.assetInfo.contentCode;
                                ob.contentName = this.assetInfo.contentName;
                                ob.ip = yh.ip;
                                ob.mac = yh.mac;
                                ob.deviceType="hd";
                                ob.userGroup = yh.userGroup;
                                ob.deviceModel = yh.brand;
                                ob.deviceSerialNo = "";
                                ob.notifyUrl = data.result.notifyUrl;
                                console.log(JSON.stringify(ob));
                                let url = location.origin+"/henan/payment?payOb="+encodeURIComponent(JSON.stringify(ob));
                                console.log("跳转支付中间页:"+url);
                                yh.openPage(url,'getPayStatus'); // 用客户端方法打开新的activity webView
                  }else if(data.result.resultCode==="3003"){
                         Toast({ message: "已订购过此套餐。", position: "bottom" });
                  }else{
                       Toast("获取BMS加密信息异常");
                       console.log("获取BMS加密信息异常");
                  }
                }else if('3003' ===data.code){
                          Toast({ message: "已订购过此套餐。", position: "bottom" });
                }else{
                    if(data.code===0||data.code==='0'){
                        if(data.message){
                            if(data.message.indexOf('SSLHandshakeException')>=0){ // https 证书时间同步失败
                                console.log('BS 捕获客户端https 时间校验不同步 请求失败，开始请求HTTP')
                                console.log('HTTP认证地址:'+auth.httpOrderUrl);
                                let contentCode = this.assetInfo.contentCode;
                                let contentName = this.assetInfo.contentName;
                                let productCode = this.dataList[this.itemNo].productCode;
                                 yh.HttpPost(auth.httpOrderUrl,auth.OrderParams(productCode, contentCode, contentName),"getProInfo"); // 支付加密
                                return;
                            }
                        }
                    }
                    Toast({ message: "BMS返回信息异常:"+data.message, position: "bottom" });
                    console.log("BMS返回信息异常");
                }
               
        },getPayStatus(data){
            const product = this.dataList[this.itemNo];
            console.log("支付页面已关闭，获取支付状态");
            $('.loading').hide();
            const payResult = localStorage.getItem("userPayStatus");
            console.log("payResult:"+payResult);
            if(payResult==="noPay"){  // 用户未支付
                this.xhaCaiji(product,"0");
                this.biCaiji(product,"0");
                this.$emit('controllMeg',true,'order','orderCancle',false);
            }else if(payResult==="paySucess"){ // 支付成功
                this.isShow = false;
                this.xhaCaiji(product,"1");
                this.biCaiji(product,"1");
                this.$emit('controllMeg',false,'order','orderCancle',true);
            }else if(payResult==="payFail"){ // 支付失败
                this.xhaCaiji(product,"0");
                this.biCaiji(product,"0");
                this.$emit('controllMeg',true,'order','orderCancle',false);
            }else{
                this.xhaCaiji(product,"0");
                this.biCaiji(product,"0");
                this.$emit('controllMeg',true,'order','orderCancle',false);
            }
        },xhaCaiji(product,type){
            try {
                    console.log(product);
                    let ProductType = 0; // 产品类型
                    if( product.cycleUnit === 4 || product.cycleUnit === '4'){
                        ProductType = 0;
                    }else if(product.cycleUnit === 5 || product.cycleUnit === '5'){
                         ProductType = 2;
                    }else if(product.cycleUnit === 6 || product.cycleUnit === '6'){
                         ProductType = 3;
                    }else if(product.cycleUnit === 3 || product.cycleUnit === 2 || product.cycleUnit === 1 || product.cycleUnit === '3' || product.cycleUnit === '2' || product.cycleUnit === '1'){
                         ProductType = 1;
                    }
                    const ProductID = product.productCode; // 产品id
                    const ProductName = encodeURIComponent(product.productName); // 产品名称
                    const EndTime =  "";
                    const Action = "1";
                    const OrderStatus = type;
                    const Fail = "";
                    const OrderUrl = encodeURIComponent(location.href);
                    const Confirmation = "0";
                    const UrlName = document.title;
                    const spCode = this.assetInfo.assetSp;
                    xha.pay(ProductType, ProductID, ProductName, EndTime, Action, OrderStatus, Fail, OrderUrl, Confirmation, UrlName,spCode);
                } catch (error) {
                    console.log("bi订购采集异常");
                }
        },biCaiji(product,type){
            try {
                const trace_id = c.getTrackPath();
                const trace_type = c.getTrackPathType();
                const parent_page_type =c.getParentPageType();
                const parent_page_id =c.getParentPageId(); 
                const special_type = "";
                const success_type = "1"; 
                const ProductType = product.cycleUnit; // 产品类型
                const ProductID = product.productCode; // 产品id
                const ProductName = encodeURIComponent(product.productName); // 产品名称
                const EndTime =  "";
                const Action = "1";
                const OrderStatus = type;
                const Fail = "";
                const OrderUrl = encodeURIComponent(location.href);
                const Confirmation = "0";
                const UrlName = document.title;
                const OperatorID = yh.operator_id;
                bi.pay(trace_id, trace_type, parent_page_type, parent_page_id, special_type, success_type, ProductType, ProductID, ProductName, OperatorID, EndTime, Action, OrderStatus, Fail, OrderUrl, Confirmation, UrlName);
            } catch (error) {
                console.log("bi订购采集异常"+error);
            }
        }
    }
}
</script>




