<!--详情页——娱乐类详情页顶部-->
<style>
.HistoryImg {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: none;
}
</style>
<template>
  <div class="Rectangle" style="box-sizing: border-box;margin-bottom: 10px">
    <div class="Mask" id="b0" v-if="!showNan">
      <div class="identifying" v-if="false">非付费用户试看5分钟</div>
      <img :src='logo' :imgClass='HistoryImg' />
    </div>
    <div class="Maskyunan" id="b0" v-if="showNan">
      <img :src=(art.assetImg) >
    </div>
    <div class="Bona">
      <div class="ss">
        <div class="text-page">
          <div class="sl1">{{art.assetName}}</div><div class="sl2"></div>
        </div>
      </div>

      <p class="text-page2">
        <span>{{art.providerMemo.split(',')[0]}}</span>
        <span>{{art.score}}</span>
        <span>{{art.area}}</span>
        <span>{{art.language}}</span>
        <span>{{art.year==0?'':art.year}}</span>
      </p>
      <p class="content-page3">{{art.description}}</p>
      <div class="advert">
        <lazy-image :src='initScores' :placeholder='placeholder' :initScore='initScore' />
      </div>
      <div class="btn-page">
        <div class="btn-play" id="b1">
          <span class="jlt4"></span>          
          {{isSouth?isAuth?isFree:isOrder:isAuth?isFree:isPay}}
          </div>
        <div class="btn-sc" id="b2">
          <span v-if="sc" class="jlt2"></span>
          <span v-if="sc">已经</span><span v-if="!sc" class="jlt1"></span>收藏
        </div>
        <div class="btn-js " id="b3" v-if="showIntroduction">
          <span class="jlt3"></span>介绍</div>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>
<style scoped="Mtop">
