  width: 35px;
  height: 35px;
  padding: 5px 7px 7px 35px;
  background: url("../../assets/icon.png") no-repeat -105px -245px;
}
</style>
<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import Toast from "../../tool/toast/toast.js";
import { xha } from "../../js/xha.js";
export default {
  props: ["initData", "jsonUrl", "isAuth", "isPreView","assetInfo","productId","FilmType"],
  name: "MovieTop",
  data() {
    return {
      showIntroduction:false,
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      initScores: require("../../assets/img_loading_160x230.png"),
      logo: require("../../../static/bg.png"),
      name: "movieTop",
      itNum: 1,
      num: 0,
      op: 1,
      sc: false,
      ss: true,
      HistoryImg: "HistoryImg",
      isShow: false,
      eleId: 0,
      pos: "",
      str: "",
      numit: 0,
      isFree: "播放",
      isOrder:"试看",// 2018/10/18 将订购修改为试看
      isPay:"试看",
      timer: null,
      art: {
        description: "",
        elementId: "",
        assetImg: "",
        assetId: "",
        assetName: "",
        layout: "",
        score: "",
        area: "",
        year: "",
        episodes: "",
        language: "",
        assetType: "",
        providerMemo: ""
      },
      collect: {
        asType: "",
        asimg: "",
        asname: "",
        sco: "",
        asId: ""
      },
      assetType:'',
      showNan: "",
      backTimes: 0,
      isInit:false,// 組件是否初始化
      EnterType:localStorage.getItem('EnterType') || 1,
    };
  },
  created() {
    window.changeEpisode = this.changeEpisode;
    window.preViewCallback = this.preViewCallback;
    this.$nextTick(function() {
      this.init();
    });
  },
  mounted() {
    // this.startScroll();
   this.$nextTick(function(){
      if(this.initData.data.description.length>46){
        this.showIntroduction = true;
      }else{
        this.showIntroduction = false;
      }
   })
  },
  methods: {
    Collect(Status){
      // 星红桉埋点
      let cId = this.initData.data.assetId; // 媒资ID
      let cName = this.initData.data.assetName; // 媒资名字
      let cStatus = Status ; // 媒资收藏状态
      xha.collect(cId, cName, cStatus);
    },
    init() {
      if (this.isSouth) {
        // 豫南
        this.showNan = true;
        if (this.isAuth) {
          // 鉴权成功
          this.$emit("controllMeg", true, "", "succeed", "");
        }
      } else {
        // 豫北
        this.showNan = false;
        this.mPlay();
      }
      this.art = this.initData.data;
      this.eleId = this.art.assetId;
      this.assetType = this.art.assetType;
      if (this.queryFavorite()) {
        // 是否收藏
        this.sc = true;
        this.ss = false;
      } else {
        this.sc = false;
        this.ss = true;
      }
    },
    startScroll() {
      // 开始滚动
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
        setTimeout(() => {
          $(".sl2").text();
          let p_title = $(".sl1").text();
          console.log("滚动文字长度" + p_title.length);
          if (p_title.length >= 11) {
            console.log("文字超出滚动");
            let speed = 40;
            let that = this;
            let ss = $('.ss')[0];
            let tab = $(".Bona .text-page")[0];
            let tab1 = $(".Bona .text-page>div:eq(0)")[0];
            let tab2 = $(".Bona .text-page>div:eq(1)")[0];
            tab.style.width = (tab1.offsetWidth*2 + 100)+'px';
            tab.style.textOverflow = "clip";
            tab2.innerHTML = tab1.innerHTML;
            this.timer = setInterval(function() {
              that.Marquee(ss, tab1, tab2);
            }, speed);
          }
        }, 10);
      });
    },
    Marquee(ss, tab1, tab2) {
      // console.log('offsetWidth:'+tab1.offsetWidth+'+++++++++++'+'scrollLeft:'+ss.scrollLeft)
      if (tab1.offsetWidth <= ss.scrollLeft) {
        ss.scrollLeft = 0;
      } else {
        ss.scrollLeft ++;
      }
    },
    changeEpisode(x) {
      console.log("单条资产试看集数:" + x);
      this.$store.commit("GET_EPISODE", x - 1); //  当退出全屏时返回集数，更新集数位置
    },
    preViewCallback(args) {
      // 回看结束通知父组件展示弹出层
      if (this.isAuth) {
        console.log("鉴权成功，第一次鉴权失败，试看回调!");
        this.play(true, !this.isAuth, "preView");
      } else {
        if(this.assetType ==="Column"){ //  综艺类没有试看不需要
           return;
        }
        console.log("鉴权失败，试看回调!");
        this.removeCss(); // 移除焦点样式
        this.isShow = false;
        yh.stop(); // 停止播放
        this.$emit("controllMeg", "", "", "preViewEnd", "");
      }
    },
    keyCode(kc) {
      if (kc == "down") {
        this.down();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "KeyEnter") {
        this.KeyEnter();
      } else if (kc == "KeyBack") {
        this.back();
      } else if (kc == "up") {
        this.up();
      }
    },
    /**
     * 查看收藏
     * @param {assetId,specialId} params
     * assetId:资产id
     * specialId:专题id
     */
    queryFavorite() {
      let storage = JSON.parse(localStorage.getItem("PersonMovies"));
      if (storage) {
        for (let i = 0; i < storage.length; i++) {
          if (storage[i].assetId == this.art.assetId) {
            return true;
          }
        }
      } else {
        return false;
      }
    },
    //收藏
    Coll() {
      //        asset_id, collect_type, type
      try {
        let pageId = this.art.assetId;
        //        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(pageId, 3, 1);
      } catch (error) {
        console.log(error);
      }
    },
    //取消收藏
    NoColl() {
      try {
        let pageId = this.art.assetId;
        //        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(pageId, 3, 2);
      } catch (error) {
        console.log(error);
      }
    },
    KeyEnter() {
      if (this.op == 0) {
        if (this.isAuth) {
          // Toast({message:"你已经订购过该产品了",position:"bottom"});
        } else {
          if (this.showNan) {
            
          } else {
            yh.stop(); // 停止播放
          }
          this.removeCss();
          this.isShow = false;
          this.$emit("controllMeg", false, "order", "vipOrder", "");
        }
      } else {
        if (this.itNum == 0) {
          if (this.showNan) {
            // 豫南版本不需要全屏
          } else {
            console.log("点击全屏");
            this.play(false, !this.isAuth, "fullScreen"); // 点击全屏
          }
        }
        if (this.itNum == 2) {
          if (!this.sc) {
            this.sc = true;
            this.ss = false;
            let Tvsc = {
              assetId: this.art.assetId,
              assetName: this.art.assetName,
              assetType: this.art.assetType,
              assetImg: this.art.assetImg,
              episodes: this.art.episodes,
              broadcast: this.art.broadcast,
              score: this.art.score,
              coverimage: this.art.coverimage,
              specialName: this.art.specialName,
              layout: this.art.layout,
              layoutUrl: this.jsonUrl
            };
            let storage = localStorage.getItem("PersonMovies");
            if (storage) {
              storage = JSON.parse(storage);
            } else {
              storage = [];
            }
            storage.push(Tvsc);
            localStorage.setItem("PersonMovies", JSON.stringify(storage));
            this.Coll();
            this.Collect(1)
          } else {
            this.sc = false;
            this.ss = true;
            let storage = JSON.parse(localStorage.getItem("PersonMovies"));
            let json = [];
            for (let i = 0; i < storage.length; i++) {
              if (i != this.findindex()) {
                json.push(storage[i]);
              }
            }
            localStorage.setItem("PersonMovies", JSON.stringify(json));
            this.NoColl();
            this.Collect(2)
          }
        } else if (this.itNum == 3) {
          if (this.showNan) {
            // 豫南版本不需要关闭播放器
          } else {
            yh.stop(false);
          }
          this.isShow = false;
          $(".btn-js").removeClass("change");
          let info = {
            title: this.art.assetName,
            msg: this.art.description,
            alert: true
          };
          this.$emit("alertM", info);
        } else if (this.itNum == 1) {
          if (this.showNan) {
            let xhalogs ={};
            try {
              let columnId = this.category[this.navpos].catId;
              let columnName = this.category[this.navpos].catName;
              let catCode = this.category[this.navpos].catCode;
              const assetId = this.eleId;
              const filmName = this.art.assetName;
              const Episodes = 1;
              const vodEpisodes = 1;
              const FilmType = 0;
              const state ="0";
              let productId = this.productId;
              let SPID = this.art.providerMemo.split(',')[0];
              if(this.EnterType == 0){
                columnName = '搜索';
                columnId = '00';
                catCode = 'search'
              }
              xhalogs = c.ynPlayLog(columnId,columnName,assetId,filmName,Episodes,vodEpisodes,this.EnterType,FilmType,state,productId,SPID);
            } catch (error) {
              console.log('豫南华为播放器传递采集信息异常')
            }
            if (this.isAuth) {
              // 鉴权通过可以播放
              let url = this.art.itemList[0].vodList[0].playUrl;
              // 点击收集历史数据
              c.getSouthHistoryData(
                this.initData.data.assetName,
                this.initData.data.assetId,
                this.initData.data.assetImg,
                this.jsonUrl,
                this.initData.data.assetType
              );
              yh.HWplay(url,false,xhalogs);
              
            } else { // 鉴权失败，试看
              // this.removeCss();
              // this.isShow = false;
              // this.$emit("controllMeg", false, "order", "vipOrder", "");
              // 鉴权通过可以播放
              // 点击收集历史数据
              c.getSouthHistoryData(
                this.initData.data.assetName,
                this.initData.data.assetId,
                this.initData.data.assetImg,
                this.jsonUrl,
                this.initData.data.assetType
              );
              let url = this.art.itemList[0].vodList[0].playUrl;
              yh.HWplay(url,true,xhalogs);
              
            }
          } else {
            //  yh.toFull(); // 点击全屏
            this.play(true, !this.isAuth, "fullSceen");
          }
        }
      }
    },
    findindex() {
      let storage = JSON.parse(localStorage.getItem("PersonMovies"));
      for (let i = 0; i < storage.length; i++) {
        if (storage[i].assetId == this.art.assetId) {
          return i;
        }
      }
    },
    clickKey() {
      //         点击事件
      try {
        let id = this.initData.data.itemList[0].itemId;
        let pageId = this.initData.data.assetId;
        let traceId = "|" + this.catId;
        let traceType = "|" + this.catCode;
        bi.categoryClick(this.catId, id, 5, 31, pageId, traceId, traceType);
      } catch (error) {
        console.log("埋点异常");
      }
    },
    mPlay() {
      //         初始化播放器
      let obj = {
        left: 57,
        top: 72,
        width: 672,
        height: 377
      };
      yh.MaxPlay(JSON.stringify(obj));
    },
    play(bud, auth, orderType) {
      // 判断播放方法 是否试看
      console.log(bud+'-----'+auth+'------'+orderType);
      try {
        let obj = {};
        let met = yh.playHistory(this.initData.data.assetId);
        console.log("影片历史" + met);
        if (c.isAllNull(met)) {
          // 历史记录为空时
          this.$store.commit("GET_EPISODE", 0); // 将集数存入vuex
          obj = {
            index: 0,
            startTime: 0,
            jsonUrl: this.jsonUrl,
            buder: bud,
            preView: auth,
            callback: "preViewCallback"
          };
        } else {
          if (typeof met == "string") {
            met = JSON.parse(met);
          }
          let recodTime = met.watchTime;
          let indexNum = met.episode - 1;
          if (auth) {
            if (orderType == "orderBack") {
              console.log("鉴权失败，且从订购弹窗返回,试看时间位0");
              recodTime = 0;
            }
          }
          this.$store.commit("GET_EPISODE", indexNum); // 将集数存入vuex
          obj = {
            index: indexNum,
            startTime: recodTime,
            jsonUrl: this.jsonUrl,
            buder: bud,
            preView: auth,
            callback: "preViewCallback"
          };
        }
        yh.play(JSON.stringify(obj));
        // this.preViewCallback();//   模拟预览结束
      } catch (error) {
        console.log("小窗口播放器失败");
      }
    },
    addCss() {
      $("#b" + this.itNum).addClass("change");
    },
    removeCss() {
      $("#b" + this.itNum).removeClass("change");
      $(".advert").removeClass("change");
    },
    right() {
      if (this.op == 0) {
        this.removeCss();
        $(".advert").addClass("change");
      } else {
        if(this.itNum == 2 && !this.showIntroduction)return;
        if (this.itNum == 3) return;
        this.removeCss();
        this.num = 1;
        this.itNum++;
        this.addCss();
      }
    },
    left() {
      if (this.op == 0) {
        this.itNum = 0;
        this.op = 1;
        this.addCss();
        $(".advert").removeClass("change");
      } else {
        if (this.itNum == 0) return;
        $(".advert").removeClass("change");
        this.removeCss();
        this.num = 1;
        this.itNum--;
        this.addCss();
      }
    },
    down() {
      if (this.op == 0) {
        this.op = 1;
        this.itNum = 1;
        $(".advert").removeClass("change");
        this.addCss();
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    up() {
      if (this.itNum == 0) return;
      this.op = 0;
      this.removeCss();
      let eleH = this.cEleOffsetTop(".btn-sc");
      this.$emit("eleScroll", this.name, "up", eleH);
      $(".advert").addClass("change");
    },
    back() {
      if (this.timer !== null) clearInterval(this.timer);
      this.$emit("sendBp", "goBack"); // 将消息发给父组件
    },
    updateStast(bl, pos, isEnd) {
      console.log(bl+"----"+pos+'^^^^^'+isEnd);
      if (bl) {
        if (this.isSouth) {
          // 是否是从介绍弹窗返回
          if (pos == "desc") {
            console.log("豫南介绍返回");
          }
        } else {
          if (pos == "desc") {
            console.log("介绍返回续播");
            setTimeout(() => {
              // 等介绍弹出层完全关闭然后再打开播放器
              this.play(false, !this.isAuth, "desc");
            }, 100);
          } else if (pos == "updateUrl") {
            console.log("相似推荐切换资产");
            this.$nextTick(function() {
              setTimeout(() => {
                // 为了不让窗口产生漂移延时
                this.play(false, !this.isAuth, "updateUrl");
              }, 100);
            });
          } else if (pos == "up") {
            console.log("底部推荐移动上来需要继续播放");
            if (yh.getPlayStatus()) {
              console.log("小窗口视频已打开");
            } else {
              setTimeout(() => {
                this.play(false, !this.isAuth, "moveTop");
              }, 100);
            }
          }
        }
        if (bl) {
          if (this.isSouth) {
            // 豫南豫北区分
            this.itNum = 1;
          } else {
            this.itNum = 0;
          }
          if (pos == "desc") {
            // 介绍返回初始化
            this.itNum = 3;
          }
          this.$nextTick(function() {
            this.addCss();
          });
        }
        this.isShow = bl;
        let eleH = this.cEleOffsetTop('.Rectangle');
        console.log("电影头部高度!"+eleH);
        this.$emit("eleScroll", this.name, pos, eleH);
      } else {
        this.isShow = false;
        this.removeCss();
      }
    },
    isAuthStatus(pos, status) {
      // BMS 鉴权支付逻辑回调
      console.log(pos, status);
      if (pos === "payInit") {
        console.log("支付成功，断点续播" + this.isAuth);
        this.initScores = require("../../assets/orderBy.png");
        this.play(false, false, "paySuccess"); //  支付成功续播
        if (this.isSouth) {
          this.itNum = 1;
        } else {
          this.itNum = 0;
        }
        this.addCss();
        this.isShow = true;
      } else if (pos == "orderBack") {
        console.log("订购弹窗返回");
        this.isShow = true;
        this.op = 1;
        if (this.isSouth) {
          this.itNum = 1;
        } else {
          this.itNum = 0;
        }
        this.$nextTick(function() {
          setTimeout(() => {
            // 为了不让窗口产生漂移延时
            this.play(false, !this.isAuth, "orderBack");
          }, 100);
        });
        this.addCss();
      } else if (pos == "AuthResult") {
        try {
          // B透传C埋点数据
          let catId =  this.category[this.navpos].catId;
          let catName = this.category[this.navpos].catName;
          let catCode = this.category[this.navpos].catCode;
          if(this.EnterType == 0){
            catName = '搜索';
            catId = '00';
            catCode = 'search'
          }
          c.logCsSplice(this.EnterType,catId,catName,this.productId,this.art.providerMemo.split(',')[0],catCode,this.eleId,this.art.assetName,1,1,this.FilmType);
        } catch (error) {
          console.log('透传客户端埋点信息异常'+error);
        }
        if (status == "paid") {
          console.log("鉴权成功!用户已支付" + status);
          this.initScores = require("../../assets/orderBy.png");
          this.play(true, false, "init");
        } else if (status == "nopay") {
          console.log("鉴权失败!用户未订购" + status);
          if (this.isSouth) {
            this.initScores = require("../../assets/south_payfor.png");
          } else {
            this.initScores = require("../../assets/payfor.png");
          }
          this.play(true, true, "init");
        } else if (status == "isfree") {
          console.log("鉴权成功!免费资产" + status);
          this.initScores = require("../../assets/nopay.png");
          this.play(true, false, "init");
        }
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 200;
      return realHeight;
    },
    getLong(ele) {
      let fEleDistance = $(ele).offset();
      return fEleDistance;
    }
  },
  computed: {
    ...mapGetters(["category", "behindParams", "isHTTPS", "navpos", "isSouth"])
  },
  watch: {
    initData() {
       this.EnterType = localStorage.getItem('EnterType') || 1;
       if(this.initData.data.description.length>46){
        this.showIntroduction = true;
      }else{
        this.showIntroduction = false;
      }
      this.art = this.initData.data;
      this.eleId = this.art.assetId;
      this.assetType = this.art.assetType;
      this.isShow = false;
      $(".btn-play").removeClass("change");
      if (this.queryFavorite()) {
        this.sc = true;
        this.ss = false;
      } else {
        this.sc = false;
        this.ss = true;
      }
      window.clearInterval(this.timer);
      this.timer = null;
      // this.startScroll();
    }
  },
  beforeRouteLeave(to, from, next) {
    try {
      window.clearInterval(this.timer);
      this.timer = null;
    } catch (error) {
    } finally {
      next();
    }
  }
};
</script>



