<!--详情页——相似推荐-->
<template>
  <div class="imgList" style="box-sizing: border-box">
    <p class="lick">{{title}}</p>
    <ul>
      <li  v-for="(item,index) in specialList"  :key="item.id" :id="('hotImg_'+eleId+'_'+index)" class="imgli animated"
           v-if="index<=5" >
        <!-- <img :src="item.assetImg" class="imgList_img" > -->
        <!--<lazy-image-->
          <!--:src='item.assetImg'-->
          <!--:placeholder='placeholder'   class="imgList_img"/>-->
        <lazy-image
          :src="item.assetImg"
          :placeholder='placeholder'
          :initScore = 'initScore'
          :score = "item.score"
          :imgClass="round"
          :title="item.assetName"
          :oragin = "item.providerId"
        />
      </li>
    </ul>
    <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style>
  .round{
    border-radius: 12px;
  }
</style>

<style scoped="Masse">
.imgList ul li .mask {
  color: black;
  position: absolute;
  left: 0;
  top: 0;
}
.lick {
  font-family: PingFangSC-Semibold;
  font-size: 28px;
  color: rgba(255, 252, 252, 0.66);
  text-align: left;
}
.imgList {
  position: relative;
  width: 1250px;
  margin-left: 30px;
  margin-top: 15px;
  margin-bottom: 50px;
}
.imgList ul:after {
  content: "";
  clear: both;
  display: block;
}
.imgList ul li {
  width: 160px;
  height: 230px;
  float: left;
  margin-right: 30px;
  position: relative;
  margin-top: 20px;
  box-sizing: border-box;
  border: 1px rgba(255, 255, 255, 0.5) solid;
  border-radius: 15px;
}
.imgList .imgList_img {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  position: relative;
}
.imgList ul .tran {
  border: 4px solid #ffffff;
  transform: scale(1.1);
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
}
</style>
<script>
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import { HTTPUtil as api } from "../../fetch/api.js";
import c from "../../js/common.js";
import { xha } from "../../js/xha.js";
export default {
  props: ["initData"],
  data() {
    return {
      name: "Movie_assetList",
      itemNo: 0,
      isShow: false,
      HistoryImg: "HistoryImg",
      placeholder: require("../../assets/img_loading_160x230.png"),
      initScore: require("../../assets/corner_mark.png"),
      eleId: "",
      isEnd: 1,
      pos: "",
      round: "round",
      itemList: [],
      arrc: [],
      detailType:'',
      specialList: [
        {
          elementId: "168453",
          layout: "Detail_Movie",
          score: "6.9",
          elementImg:
            "http://dx-bsyn.hilooktv.com:8001/img/epg/2017-09-18/bdf07478a37daada12bf9c9ef61fd93c.jpg",
          jsonUrl:
            "http://dx-bsyn.hilooktv.com:8001/?s=1000|1&p=yhAssetDetail&k=1&v=1&assetId=168453",
          elementType: 1,
          elementName: "湘江北去"
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["behindParams", "isSouth"])
  },
  created() {
    this.specialList = this.initData.data.assetList;
    this.eleId = this.initData.data.assetList[0].assetId+1; // 默认数据第一个为组件内的id
    this.name = "Movie_assetList"; // 默认数据第一个为组件名字
    this.detailType = this.initData.data.assetType;
    this.title = "相似推荐"; // 默认标题
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  updated() {
    this.$nextTick(function() {
      this.$lazyImages.loadImage();
    });
  },
  methods: {
    pageLoad(name){
      // 星红桉埋点
      let SrcPagePath = "";
      let PageName = name;
      let AsssetID = this.initData.data.assetId;
      let Id = localStorage.getItem("position").split('_')[0];
      let TYPE = "";
      xha.pageLoad("详情页关联推荐",SrcPagePath,PageName,AsssetID,Id,TYPE);
    },
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        let url = this.specialList[this.itemNo].jsonUrl;
        let name = this.specialList[this.itemNo].assetName;
        // let nextType = this.specialList[this.itemNo].assetType;
        this.pageLoad(name);
        this.isShow = false;
        this.$emit("controllMeg", this.isShow, "updateUrl", "KeyEnter", url);
        this.removeCss();
        window.localStorage.setItem('EnterType','5');
      }
    },
    back() {
      this.$emit("sendBp", "goBack"); // 将消息发给父组件
    },
    down() {
      if (this.isEnd) {
        this.removeCss();
        this.addCss();
      } else {
        this.removeCss();
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
      }
    },
    up() {
      this.removeCss();
      //        let eleH = this.cEleOffsetTop('#hotImg_'+this.eleId+"_"+this.itemNo+'');
      this.$emit("pressDown", this.isShow, this.eleId, "up");
      this.$emit("eleScroll", this.name, "up");
      this.isShow = false;
    },
    left() {
      if (this.itemNo == 0 || this.itemNo == 6) return;
      this.removeCss();
      this.itemNo--;
      this.addCss();
    },
    right() {
      if (this.itemNo == 5 || this.itemNo == 11) return;
      this.removeCss();
      this.itemNo++;
      this.addCss();
    },
    addCss() {
      $("#hotImg_" + this.eleId + "_" + this.itemNo+" .imglazy").removeClass("tran");
      $("#hotImg_" + this.eleId + "_" + this.itemNo+" .imglazy").addClass("tran");
      let word = this.specialList[this.itemNo].assetName;
      // if(word.length>8){
      //   $("#hotImg_" + this.eleId + "_" + this.itemNo+" .s-title-name").html("<marquee scrolldelay='200' behavior='scroll'>"+word+"</marquee>");
      // }
    },
    removeCss() {
      $("#hotImg_" + this.eleId + "_" + this.itemNo+" .imglazy").addClass("tran");
      $("#hotImg_" + this.eleId + "_" + this.itemNo+" .imglazy").removeClass("tran");
      let word = this.specialList[this.itemNo].assetName;
      // if(word.length>8){
      //   $("#hotImg_" + this.eleId + "_" + this.itemNo+" .s-title-name").html(word);
      // }
    },
    updateStast(bl, pos, isEnd) { 
      this.isEnd = isEnd;
      this.isShow = bl;
      this.addCss();
      if (bl) {
        if (pos == "down") {
          let eleH = this.cEleOffsetTop(
            "#hotImg_" + this.eleId + "_" + this.itemNo + ""
          );
          if(isEnd)return;
          this.$emit("eleScroll", this.name, pos, eleH);
        }
      } else {
        this.removeCss();
      }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 300;
      return realHeight;
    },
    rnd(n, m) {
      return parseInt(Math.random() * (m - n) + n);
    }
  },
  components: {},
  watch: {
    initData() {
      this.specialList = this.initData.data.assetList;
      this.eleId = this.initData.data.assetList[0].assetId+1; // 默认数据第一个为组件内的id
      this.name = "Movie_assetList"; // 默认数据第一个为组件名字
      this.detailType = this.initData.data.assetType;
      this.itemList = this.specialList.sort(function() {
        return 0.5 - Math.random();
      });
    }
  }
};
</script>




