<!--详情页——单选集层-->
<style scoped="GameList">
.ption-top.border-ption {
  background: rgba(255, 255, 255, 0.6);
}

.ption-top {
  width: 1170px;
  height: 70px;
  position: relative;
  overflow: hidden;
  margin-top: 3px;
  border-radius: 5px;
  background: linear-gradient(
    to left,
    rgba(255, 255, 255, 0.01) 0%,
    rgba(255, 255, 255, 0.4) 50%,
    rgba(255, 255, 255, 0.01) 100%
  );
  box-sizing: border-box;
}
.ptionx {
  width: 100000px;
  height: 70px;
  overflow: hidden;
  position: absolute;
  left: 450px;
  top: 0;
  transition: all 0.1s;
}
.ptionx:after {
  clear: both;
  content: "";
  display: block;
}
.ptionL {
  text-align: center;
  opacity: 0.66;
  background: white;
  width: 200px;
  height: 100%;
  position: absolute;
  left: 450px;
  top: 0;
  box-sizing: border-box;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.75);
}
.ptionx .text_num {
  width: 200px;
  height: 70px;
  font-family: PingFangSC-Regular;
  font-size: 18px;
  text-align: center;
  float: left;
  line-height: 68px;
  color: #ffffff;
  position: relative;
}
.s-title {
  width: 100%;
  /*position:absolute;*/
  left: 0;
  display: inline-block;
  z-index: 99;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: white;
}
.s-title-name,
.s-title-temp {
  margin-right: 15px;
  display: inline;
}
.s-title-name{
  margin-left: 12px;
}

</style>

<template>
  <div style="box-sizing: border-box;margin-top: 30px"  class="pt" >
    <div class="ption-top border-ption" id="pt1">
      <div class="ptionL"></div>
      <div class="ptionx">
        <div v-for="(item,index) in itemName" v-if="index>=0" :key="item.id" :id="('c'+index)" class="text_num">
          <div class="s-title">
            <div class="s-title-name">{{item}}</div>
            <div class="s-title-temp"></div>
          </div>
        </div>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>

<script scoped="itemliss">
import { mapGetters } from "vuex";
import { client as yh } from "../../js/client.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";

export default {
  props: ["initData", "jsonUrl", "backIng", "isAuth", "isPreView","assetInfo","productId","FilmType"],
  data() {
    return {
      name: "itemList",
      itemNo: 0,
      num: 0,
      isShow: false,
      timer: null,
      top: [
        {
          episode: ""
        }
      ],
      itemName: [],
      EnterType:localStorage.getItem('EnterType') || 1,
    };
  },
  computed: {
    ...mapGetters(["behindParams", "isSouth", "episode","category", "navpos"])
  },
  mounted() {
    this.top = this.initData.data.itemList;
    this.eleId = this.initData.data.itemList[0].itemId;
    for (let i = 0; i < this.top.length; i++) {
      let item = this.top[i].itemName;
      this.itemName.push(item);
    }
    this.$nextTick(function() {
      this.removeCss();
      if (this.episode !== undefined) {
        // 将vuex集数取出重新设置
        this.num = this.episode;
        $(".ptionx").css({
          transition: "all 0.3s",
          transform: "translateX(-" + 200 * this.num + "px)"
        });
      }
    });
  },
  methods: {
    keyCode(kc) {
      if (kc == "KeyBack") {
        this.back();
      } else if (kc == "right") {
        this.right();
      } else if (kc == "left") {
        this.left();
      } else if (kc == "down") {
        this.down();
      } else if (kc == "up") {
        this.up();
      } else if (kc == "KeyEnter") {
        if (this.isSouth) {
          let xhalogs = {};
          try {
              let columnId = this.category[this.navpos].catId;
              let columnName = this.category[this.navpos].catName;
              const assetId = this.initData.data.assetId;
              const filmName = this.initData.data.assetName;
              const Episodes = this.num;
              const vodEpisodes = this.initData.data.itemList.length;
              const state ="0";
              let productId = this.productId;
              let SPID = this.initData.data.providerMemo.split(',')[0];
               if(this.EnterType == 0){
                columnName = '搜索';
                columnId = '00';
                catCode = 'search'
              }
              xhalogs = c.ynPlayLog(columnId,columnName,assetId,filmName,Episodes,vodEpisodes,this.EnterType,this.FilmType,state,productId,SPID);
            } catch (error) {
              console.log('豫南华为播放器传递采集信息异常')
            }
          c.getSouthHistoryData(
            this.initData.data.assetName,
            this.initData.data.assetId,
            this.initData.data.assetImg,
            this.jsonUrl,
            this.initData.data.assetType
          );
          let url = this.initData.data.itemList[this.num].vodList[0].playUrl;
          yh.HWplay(url,false,xhalogs);
        } else {
          this.KeyEnter(false);
        }
      }
    },
    back() {
      this.$emit("sendBp", "goBack"); // 将消息发给父组件
    },
    right() {
      this.removeCss();
      if (this.num >= this.top.length - 1) return;
      this.num += 1;
      $(".ptionx").css({
        transition: "all 0.3s",
        transform: "translateX(-" + 200 * this.num + "px)"
      });
      this.addCss();
    },
    left() {
      this.removeCss();
      this.num--;
      $(".ptionx").css({
        transition: "all 0.3s",
        transform: "translateX(-" + 200 * this.num + "px)"
      });
      if (this.num <= 0) {
        this.num = 0;
        $(".ptionx").css({
          transition: "all 0.3s",
          transform: "translateX(-" + 200 * this.num + "px)"
        });
      }
      this.addCss();
    },
    up() {
      $("#pt1").removeClass("border-ption");
      $(".ptionL").css("background", "white");
      this.isShow = false;
      this.$emit("pressDown", this.isShow, this.eleId, "up");
      let eleH = this.cEleOffsetTop(".ptionL");
      this.$emit("eleScroll", this.name, "up", eleH);
      this.removeCss();
    },
    down() {
      if (this.isEnd) {
        this.removeCss();
        this.addCss();
      } else {
        $("#pt1").removeClass("border-ption");
        $(".ptionL").css("background", "white");
        this.isShow = false;
        this.$emit("pressDown", this.isShow, this.eleId, "down");
        let eleH = this.cEleOffsetTop(".pt");
        this.$emit("eleScroll", this.name, "down", eleH);
        this.removeCss();
      }
    },
    updateStast(bl, pos, isEnd) {
      console.log(bl+"|"+pos+"|"+ isEnd);
      this.isShow = bl;
      if (bl) {
        if (pos === "payInit") {
          console.log("支付成功，断点续播" + this.isAuth);
          this.play(false, false); //  支付成功续播
        } else if (pos == "updateUrl") {
          console.log("相似推荐切换资产");
        }
        this.addCss();
        $("#pt1").addClass("border-ption");
        $(".ptionL").css("background", "#0097BB");
      } else {
        this.removeCss();
      }
    },
    addCss() {
      let p_title = $(
        ".ptionx .text_num:eq(" + this.num + ") .s-title-name"
      ).text();
      if (p_title.length >= 8) {
        let id = ".text_num";
        // let speed = 40;
        // let that = this;
        // let tab = document.querySelectorAll(id + " .s-title")[this.num];
        let tab1 = document.querySelectorAll(id + " .s-title-name")[this.num].innerText;
        document.querySelectorAll(".text_num .s-title-name")[this.num].innerHTML="<marquee scrollamount='4'>"+tab1+"</marquee>";
        // let tab2 = document.querySelectorAll(id + " .s-title-temp")[this.num];
        // tab.style.textOverflow = "clip";
        // tab2.innerHTML = tab1.innerHTML;
        // window.clearInterval(this.timer);
        // this.timer = null;
        // this.timer = setInterval(function() {
        //   that.Marquee(tab, tab2);
        // }, speed);
      }
    },
    Marquee(tab, tab2) {
      if (tab2.offsetWidth - tab.scrollLeft <= 12 ) {
        // tab.scrollLeft -= tab2.offsetWidth;
        tab.scrollLeft = 0; // 上面的写法会导致滚动时突然抖动
      } else {
        tab.scrollLeft++;
      }
    },
    removeCss() {
      // if (this.timer != null) {
      //   clearInterval(this.timer);
      //   this.timer = null;
      let id = ".text_num";
      let tab1 = document.querySelectorAll(id + " .s-title-name")[this.num].innerText;
      document.querySelectorAll(".text_num .s-title-name")[this.num].innerHTML=tab1;
      //   tab.scrollLeft = 0;
      //   tab.style.textOverflow = "ellipsis";
      // }
    },
    cEleOffsetTop(ele) {
      let fEleDistance = $(ele).offset();
      let realHeight = fEleDistance.top + 70;
      return realHeight;
    },
    KeyEnter(bud) {
      let tempUrl = this.jsonUrl;
      let obj = JSON.stringify({
        index: this.num,
        startTime: 0,
        jsonUrl: tempUrl
      });
      let id = this.initData.data.itemList[this.num].itemId;
      let pageId = this.initData.data.assetId;
      let traceId = this.catId;
      let traceType = this.catCode;
      bi.categoryClick(this.catId, id, 3, 31, pageId, traceId, traceType);
      this.$store.commit("GET_EPISODE", this.num); // 将集数存入vuex
      obj = {
        index: this.num,
        startTime: 0,
        jsonUrl: tempUrl,
        buder: bud,
        preView: !this.isAuth,
        callback: "preViewCallback"
      };
      yh.play(JSON.stringify(obj));
    }
  },
  watch: {
    initData() {
      this.top = this.initData.data.itemList;
      this.eleId = this.initData.data.itemList[0].itemId;
      this.itemName = [];
      this.num = 0;
      $(".ptionx").css({
        transition: "all 0.3s",
        transform: "translateX(-" + 200 * this.num + "px)"
      });
      for (let i = 0; i < this.top.length; i++) {
        let item = this.top[i].itemName;
        this.itemName.push(item);
      }
    },
    episode() {
      console.log(
        "单条退出全屏集数有更新,同步更新选集条,当前选集： " + this.episode
      );
      try {
        if (this.episode !== undefined) {
          this.num = this.episode;
          $(".ptionx").css({
            transition: "all 0.3s",
            transform: "translateX(-" + 200 * this.num + "px)"
          });
        }
      } catch (error) {
        console.log("显示集数异常");
      }
    }
  }
};
</script>



