<!--重用详情页-->
<template>
  <div class="box">
    <div class="scroll-box">
      <component v-for="(item,index) in child" :is="item.ele" :key="index" v-on:pressDown="keepDo" v-on:controllMeg="componentmMsg"
                 v-on:eleScroll="changeHeight" v-on:alertM="openMsg" v-on:sendBp="sendBp" ref="childDo"
                 :initData="item.initData" :jsonUrl="childrenUrl" :isEnd="isEnd" :isAuth="isAuth" :isPreView="isOrder" :assetInfo="assetInfo" :productId="pid" :FilmType="FilmType"></component>
    </div>
    <description ref="description" :isAlert="alertMsg" v-on:press="kDo"></description>
    <order v-if="isOrder" v-on:controllMeg="componentmMsg" :orders="orderList" :assetInfo="assetInfo" ></order>
  </div>
</template>
<style scoped="detail">
.time,
.date {
  font-size: 20px;
  float: right;
  margin-right: 170px;
  line-height: 60px;
  position: absolute;
  right: 50px;
  top: -55px;
}
.date {
  margin-right: 60px;
  color: rgba(255, 255, 255, 0.7);
}
.box {
  background: linear-gradient(to bottom, #0e314b 0, #0e314b 100%);
  position: relative;
  width: 1280px;
  height: 720px;
  overflow: hidden;
}
.box .scroll-box {
  margin-left: 51px;
  margin-top: 66px;
  width: 1229px;
  position: absolute;
  transform: inherit;
}
</style>
<script>
import movie_top from "../detail/movie_top.vue";
import special from "../detail/Category_Movie_special.vue";
import Movie_assetList from "../detail/Category_Movie_assetList.vue";
import Movie_actorList from "../detail/Category_Movie_actorList.vue";
import itemList from "../detail/Category_Movie_itemList.vue";
import ption from "../detail/ption.vue";
import seriesTop from "../detail/seriesTop.vue";
import description from "../detail/Category_Movie_description.vue";
import { client as yh } from "../../js/client.js";
import { mapGetters } from "vuex";
import { HTTPUtil as api } from "../../fetch/api.js";
import { bp as bi } from "../../js/ga.js";
import c from "../../js/common.js";
import { xha } from "../../js/xha.js";
import { Authentication as auth } from "../../js/auth.js";
import order from "../../tool/order/order.vue";
import Toast from "../../tool/toast/toast.js";
export default {
  props: ["jsonUrl"],
  data() {
    return {
      componentsName: [], // 组件名称
      child: [], // 子组件
      componentsId: [], // 组件ID
      cheight: [], // 各个组件移动的高度
      eleId: "", // 父组件ID
      isAuth: false, // 鉴权结果，默认是鉴权不通过,根据接口返回来判断是否需要试看
      isEnd: false, // 是否已经是最后一个组件
      childrenUrl: "", 
      nummc: 0,
      pageSize: 0, // 
      pageId: 0,
      traceIds: "",
      old: 0,
      routerss: false,
      oldTimes: 0,
      oldTime: 0,
      special: [],
      traceTypes: "",
      isOrder: false, // 是否弹窗试看
      orderList: null, // 可订购列表
      assetInfo: null,
      timeOutTimess: 0, // 定时任务
      authStartTime: 0, // 鉴权开始时间
      authEndTime: 0, // 鉴权结束时间
      pid: 0, // 可订购产品包ID
      sid: 0, // 可订购产品包服务ID
      sname: "", // 可订购产品名称
      alertMsg: {
        title: "",
        msg: "",
        alert: false
      },
      detailData: null,
      columnId: 0, // 栏目ID
      returnTimes: 0,
      waitting: "init",
      isdestory: false, // 判断页面是否销毁
      authCode:0, // 鉴权code
      selection:false,
      detailType:'', // 详情页类型
      isInit:false,
      FilmType:'',
      Episodes:1,
      AllEpisodes:0,
      isLiveJump:false
      // jsonUrl:'http://183.131.15.11:8380/?s=19|7&p=yhAssetDetail&k=1&v=1&assetId=3448',
    };
  },
  created() {
    this.init(this.waitting);
    window.productAuth = this.productAuth;
    window.restToke = this.restToke;
  },
  activated(){
    // $('.box').css('background-image','url('+this.category[0].bgPhoto+')') // 详情页背景图去掉
  },
  mounted() {
    this.$nextTick(function() { 
      $(".openImage").hide();
      $(".loading").hide();
      yh.close_s_Video(); // 防止在详情页直播窗口没有关闭
      // $('.box').css('background-image','url('+this.category[0].bgPhoto+')')
    });
  },
  methods: {
    getAssentsPPV() {
      // CMS 查询绑定关系
      api
        .getProducts(this.pageId, this.columnId, this.isSouth)
        .then(jsonData => {
          if (this.isdestory) {
            console.log("页面已销毁,查询资产绑包逻辑处理终止");
            return false;
          }
          console.log(JSON.stringify(jsonData));
          if (jsonData.code == 200) {
            if (jsonData.data.result.length > 0) {
              console.log("收费资产：资产ID:" + this.pageId);
              const pob = jsonData.data.result[0].pgList[0];
              console.log(pob);
              this.pid = pob.productCode;
              this.sid = pob.serviceCode;
              this.sname = pob.serviceName;
              console.log("pob.productCode=======" + pob.productCode);
               if (process.env.NODE_ENV === "development") { // 开发环境使用
                  let params = {};
                    params.url = "/api/Auth";
                    params.type = "post";
                    params.data = JSON.parse(auth.AuthParams(this.pid,this.sid,this.pageId));
                    api.ajax(params).then((jsonData)=>{
                    this.productAuth(jsonData);
                  })
               }
              this.authStartTime = new Date().getTime();
              yh.HttpPost(
                auth.Auth,
                auth.AuthParams(this.pid, this.sid, this.pageId),
                "productAuth"
              ); // 产品包鉴权请求
            } else {
              this.filterRefs().isAuthStatus("AuthResult", "isfree");
              console.log("免费资产直接播");
              this.isAuth = true;
            }
          }
        })
        .catch(() => {
          if (this.isdestory) {
            console.log("页面已销毁,查询资产绑包信息处理终止");
            return false;
          }
          Toast({ message: "CMS查询产品包信息异常", position: "bottom" });
          this.filterRefs().isAuthStatus("AuthResult", "nopay");
          this.isAuth = false;
          console.log("查询绑定关系失败,试看");
          yh.updatePreViwe(true);
          if(this.detailType==="Series"){
              this.getSelectionRef().IsShowVip(true);
          }
        });
    },
    productAuth(jsonData) {
      // BMS产品鉴权
      if (this.isdestory) {
        console.log("页面已销毁,BMS逻辑处理终止");
        return false;
      }
      this.authEndTime = new Date().getTime() - this.authStartTime;
      console.log("BMS响应时间:" + this.authEndTime);
      console.log("请求BMS 鉴权返回:" + JSON.stringify(jsonData));
      if (jsonData.code == 200) {
        if (jsonData.result) {
          // 存在可订购列表
          let code = jsonData.result.resultCode;
          this.authCode  = code;
          if (code == "0000") {
            console.log("已订购");
            this.isAuth = true;
            yh.updatePreViwe(false); // 鉴权成功通知播放器无需试看
            this.filterRefs().isAuthStatus("AuthResult", "paid");
          } else if (code == "2002") {
            console.log("鉴权未通过，服务异常");
            this.isAuth = true;
            yh.updatePreViwe(false); // 鉴权成功通知播放器无需试看
            this.filterRefs().isAuthStatus("AuthResult", "isfree");
          } else if(code == "2004"){
            Toast("鉴权失败，站点ID异常!请退出重试");
            this.orderList = jsonData.result.products;
            this.isAuth = false;
            console.log("未订购,试看");
            yh.updatePreViwe(true);
            this.filterRefs().isAuthStatus("AuthResult", "nopay");
            if(this.detailType==="Series"){
              this.getSelectionRef().IsShowVip(true);
            }
          }else if(code=="1002" || code == "1001" || code == "1005"){
            Toast({ message: "鉴权失败，账号不存在或非法。", position: "bottom" });
            this.orderList = {code:1002,msg:"账户不存在或账号非法"};
            this.isAuth = false;
            console.log("账户不存在或非法，请联系客服解决。");
            yh.updatePreViwe(true);
            this.filterRefs().isAuthStatus("AuthResult", "nopay");
            if(this.detailType==="Series"){
              this.getSelectionRef().IsShowVip(true);
            }
          }else{
            this.orderList = jsonData.result.products;
            this.isAuth = false;
            console.log("未订购,试看");
            yh.updatePreViwe(true);
            this.filterRefs().isAuthStatus("AuthResult", "nopay");
            if(this.detailType==="Series"){
              this.getSelectionRef().IsShowVip(true);
            }
          }
        }
      } else if (jsonData.code === 1004 || "1004" === jsonData.code) {
        // 用户token失效，续租
        console.log("token令牌失效重新续租");
        this.isAuth = false;
        if(this.detailType==="Series"){
            this.getSelectionRef().IsShowVip(true);
        }
        this.restUserToken();
      } else {
        if(jsonData.code===0||jsonData.code==='0'){
              if(jsonData.message){
                  if(jsonData.message.indexOf('SSLHandshakeException')>=0){ // https 证书时间同步失败
                      
                    console.log('BS 捕获客户端https 时间校验不同步 请求失败，开始请求HTTP')
                     console.log('HTTP鉴权地址:'+auth.httpAuth);
                     yh.HttpPost(
                      auth.httpAuth,
                      auth.AuthParams(this.pid, this.sid, this.pageId),
                      "productAuth"
                    ); // 产品包鉴权请求
                     // yh.stop(); // 如果用户在小窗口观看中，认证失败,需要关闭点播小窗口
                      // this.$router.push({name:"dailog",params:{msg:"本地时间校验失败,请重启机顶盒！",code:101}});
                      return;
                  }
              }
        }
        this.isAuth = true; // 鉴权超时
        // Toast({message:"用户鉴权异常,请退出重试",position:'bottom'});
        yh.updatePreViwe(false); // 鉴权超时通知播放器无需试看
        this.filterRefs().isAuthStatus("AuthResult", "isfree");
      }
    },
    restUserToken() {
      // 续租token
      yh.HttpPost(auth.Login, auth.LoginParams(), "restToke"); // 终端请求
    },
    restToke() {
      // BMS 续租结果处理
      if (jsonData.code == 200) {
        if (jsonData.result.resultCode == "0000") {
          try {
            yh.setAuthInfo(jsonData.result);
            localStorage.setItem("AAA", "true");
            localStorage.setItem("AuthResult", JSON.stringify(jsonData.result));
            this.$store.commit("GET_AUTH", "true");
            yh.HttpPost(
              auth.Auth,
              auth.AuthParams(this.pid, this.sid, this.pageId),
              "productAuth"
            ); // 续租成功继续鉴权
          } catch (error) {
            console.log("鉴权失败，处理参数转换异常");
          }
        } else {
          localStorage.setItem("AAA", "false");
          this.$store.commit("GET_AUTH", "false");
          Toast("续租token失败,用户账号异常");
        }
      } else {
        Toast("续租token失败,请退出详情页重试");
      }
    },
    init(status) {
      let curl = "";
      if (this.$route.query.jsonUrl) {
        /** 从直播关联推荐进到详情页 */
        curl = this.$route.query.jsonUrl + this.behindParams;
        this.childrenUrl = this.$route.query.jsonUrl;
        this.isLiveJump = true;
        // CS页面跳转详情页需要改变进入方式
        localStorage.setItem('EnterType','5')
      } else {
        if (this.jsonUrl) {
          this.childrenUrl = this.jsonUrl;
          curl = this.jsonUrl + this.behindParams;
          localStorage.setItem("address", this.jsonUrl);
        } else {
          curl = localStorage.getItem("address") || "";
          this.childrenUrl = curl;
          this.jsonUrl = curl;
          curl += this.behindParams;
        }
      }
      this.getDetail(curl, status);
    },
    keepDo(bl, name, pos, url) {
      // 处理组件之间的键值调用
      let childComponents = this.$refs.childDo;
      let componentsSize = childComponents.length;
      for (let i = 0; i < childComponents.length; i++) {
        //遍历组件ref 给对应的组件排序
        if (name == childComponents[i].eleId) {
          // 当对应组件上下没有焦点时会抛出控制权，此时父组件需要根据子组件抛出的信息来决定控制权交给其他父组件
          let _num = this.componentsId.indexOf(name);
          if (pos == "down") {
            if (_num == componentsSize - 1) {
              console.log('不能往下移了')
              this.getChildRef(name).updateStast(true, pos, true);
            } else {
              let _cdown = this.componentsId[_num + 1];
              this.getChildRef(_cdown).updateStast(true, pos, false);
            }
          } else if (pos == "up") { 
            if (_num == 0) {
              $(".scroll-box").animate({ top: "0px" }, 100);
            } else {
              let _cdown = this.componentsId[_num - 1];
              this.getChildRef(_cdown).updateStast(true, pos, false);
            }
          }
          break; // 找到组件需终止循环foreach则不会，需要抛异常才会终止
        }
      }
    },
    componentmMsg(bl, name, pos, url) {
      // 处理支付组件之间的消息
      console.log(bl+"|"+ name+"|"+ pos+"|"+ url);
      let childComponents = this.$refs.childDo;
      if (pos == "KeyEnter") {
        $(".scroll-box")
          .stop(false, true)
          .animate({ top: "0px" }, 100);
        this.pageSize = 0;
        this.cheight = [];
        this.childrenUrl = url;
        this.jsonUrl = url;
        this.waitting = "updateUrl";
        this.getDetail(url + this.behindParams, "updateUrl");
        return;
      } else if (pos === "preViewEnd" || pos === "vipOrder") {
        console.log("预览结束,展示弹出层");
        if(this.authCode=="1001"||this.authCode==1001 ||this.authCode=="1002" ||this.authCode=="1002" || this.authCode == "1005" ){
          Toast({ message: "鉴权失败，账号不存在或非法。", position: "bottom" });
          console.log("试看结束，用户鉴权失败，该用户不存在，重新试看!");
          this.filterRefs().isAuthStatus("orderBack", true);
        }else{
          this.$nextTick(function() {
            if(url==="selection"){
              this.selection = true;
            }
            this.closeKeyDo();
            this.isOrder = true;
          });
        }
        
      } else if (pos === "orderCancle") {
        //  关闭订购弹出层,将键值重新传给组件
        $(".openImage").hide();
        if (url === true) {
          this.isAuth = true; // 订购成功鉴权为true
          Toast("订购成功！");
        } else if (url === false || url == "false") {
          console.log("支付失败啦，留在弹窗页面");
          this.isAuth = false;
          this.isOrder = true; // 订购通知未付款或失败显示弹窗，用户手动关闭
          // Toast("订购失败~");
          return false;
        }
        this.isOrder = false; // 弹窗消失
        console.log("支付逻辑结束,清空支付状态");
        localStorage.setItem("userPayStatus", "");
        if (this.detailData.assetType == "Series") {
          // 如果是电视剧详情页则初始化在选集上
          console.log("支付成功!电视剧详情页通知");
          this.filterRefs().isAuthStatus("payInit", true);
        } else if (this.detailData.itemList[1]) {
          // 如果是单条有子集的话
          console.log("支付成功!单条通知");
          this.filterRefs().isAuthStatus("payInit", true);
        } else {
          //  如果电影或是没有子集的情况
          console.log("支付成功!电影通知");
          this.filterRefs().isAuthStatus("payInit", true);
        }
      } else if (pos === "orderBack") {
        this.isOrder = false; // 弹窗消失
        console.log("支付逻辑结束,清空支付状态");
        localStorage.setItem("userPayStatus", "");
        if (this.detailData.assetType == "Series") {
          // 如果是电视剧详情页则初始化在选集上
          console.log("支付页回调!电视剧详情页通知");
          if(this.selection){
            this.selection = false;
            this.filterRefs().isAuthStatus("orderBack", false); // 初始化播放器
            if(this.componentsId.length>2){
              this.getSelectionRef().updateStast(true,"orderBack",false); // 初始化选集条
            }else{
              this.getSelectionRef().updateStast(true,"orderBack",false); // 初始化选集条
            }
          }else{
            this.filterRefs().isAuthStatus("orderBack", false);

            if(this.componentsId.length>2){
              this.getSelectionRef().updateStast(true,"orderBack",false); // 初始化选集条
            }else{
              this.getSelectionRef().updateStast(true,"orderBack",false); // 初始化选集条
            }
          }
        } else if (this.detailData.itemList[1]) {
          // 如果是单条有子集的话
          console.log("支付页回调!单条通知");
          this.filterRefs().isAuthStatus("orderBack", true);
        } else {
          //  如果电影或是没有子集的情况
          console.log("支付页回调!电影通知");
          this.filterRefs().isAuthStatus("orderBack", true);
        }
      }
    },
    closeKeyDo() {
      // 消除所有子组件的样式
      let childComponents = this.$refs.childDo;
      for (let i = 0; i < childComponents.length; i++) {
        //遍历组件ref给isShow=fasle
        childComponents[i].updateStast(false, "removeCss", false);
      }
    },
    getChildRef(id) {
      // 根据元素ID查找ref中的组件
      let childComponents = this.$refs.childDo;
      for (let i = 0; i < childComponents.length; i++) {
        if (id == childComponents[i].eleId) {
          return childComponents[i];
        }
      }
    },
    kDo(bl, name, pos) {
      if (pos == "KeyBack") {
        this.filterRefs().updateStast(true, "desc", false);
      }
    },
    changeHeight(name, pos, height) {
      console.log('高度'+name, pos, height);
      if (pos == "down") {
        if (height > 720) {
          yh.stop(false);
          let _temp = height - 700;
          this.cheight.push(_temp);
          $(".scroll-box")
            .stop(false, true)
            .animate({ top: "-=" + _temp }, 100);
          this.pageSize++;
        }
      } else if (pos == "up") {
        if (this.pageSize == 0) {
          if (this.isSouth) {
            console.log("豫南版本播放");
          } else {
            console.log("向上不需要播放");
          }
          this.pageSize = 0;
          $(".scroll-box")
            .stop(false, true)
            .animate({ top: "0px" }, 100);
        } else {
          this.pageSize = this.pageSize - 1;
          $(".scroll-box")
            .stop(false, true)
            .animate({ top: "+=" + this.cheight[this.pageSize] }, 100);
          this.cheight.splice(this.pageSize, 1);
        }
      }
    },
    openMsg(obj) {
      this.alertMsg = obj;
    },
    sendBp(url) {
      if (url == "goBack") {
        if (this.isSouth) {
          // 豫南版本返回
          console.log("豫南版本返回");
          if (yh.isLive()) {
            console.log('直播跳点播返回')
            yh.finishOpen(); // 判断是否是直播跳点播，需要关闭activity
          } else {
            if (localStorage.getItem("next") == "返回首页") {
              localStorage.removeItem("next");
              this.$router.push({ name: "index" });
            } else {
               if (this.$route.query.jsonUrl != undefined) {
                  yh.back();
              } else{

                setTimeout(() => {
                  this.returnTimes++;
                  if (this.returnTimes > 1) {
                    this.$router.push({ name: "index" });
                  } else {
                    const backUrl = localStorage.getItem("detailback");
                    if ("hzindex" === backUrl) {
                      console.log("从专区返回退出");
                      this.$router.push({ name: backUrl });
                    } else {
                      this.$router.go(-1);
                    }
                  }
                }, 50);
              }
            }
          }
        } else {
          // 豫北版本返回
          console.log("豫北版本返回");
          this.isdestory = true;
          yh.stop(true);
          try {
            if (yh.isLive()) {
              yh.finishOpen(); //            销毁详情页
            } else {
              if (localStorage.getItem("next") == "返回首页") {
                localStorage.removeItem("next");
                this.$router.push({ name: "index" });
              } else {
                if (this.$route.query.jsonUrl != undefined) {
                  yh.back();
                } else {
                  setTimeout(() => {
                    this.returnTimes++;
                    if (this.returnTimes > 1) {
                      this.$router.push({ name: "index" });
                    } else {
                      const backUrl = localStorage.getItem("detailback");
                      if ("hzindex" === backUrl) {
                        console.log("从专区返回退出");
                        this.$router.push({ name: backUrl });
                      } else {
                        this.$router.go(-1);
                      }
                    }
                  }, 50);
                }
              }
            }
          } catch (error) {
            console.log("详情页返回异常");
            this.$router.push({ name: "index" });
          }
        }
      } else {
        c.routerSkip(url, "2", "", this.$router);
      }
    },
    ds() {
      setInterval(function() {
        var date = new Date();
        var h = date.getHours(); //获取小时
        var m = date.getMinutes(); //获取分钟
        var year = date.getFullYear() + "-"; //获取年
        var month = date.getMonth() + 1 + "-"; //获取月
        var day = date.getDate(); //获取日期
        $(".date").text(year + month + day);
        if (h < 10) {
          $(".time").text("0" + h + ":" + m);
        } else {
          $(".time").text(h + ":" + m);
        }
        if (m < 10) {
          $(".time").text(h + ":0" + m);
        } else {
          $(".time").text(h + ":" + m);
        }
      }, 1000);
    },
    getDetail(curl, status) {
      console.log("详情页状态:" + status);
      if (this.nummc >= 1) {
        let time = new Date().valueOf();
        let times = time - this.oldTimes;
        this.pageId = this.special.assetId;
        let catCode = c.getParentPageType();
        c.setTrackPath(this.pageId, 3);
        let traceId = c.getTrackPath();
        let traceType = c.getTrackPathType();
        let catId = c.getParentPageId();
        bi.closPage(
          traceId,
          traceType,
          31,
          this.pageId,
          catCode,
          catId,
          0,
          times
        );
      }
      this.old = new Date().valueOf();
      let that = this;
      let purl = "";
      if (curl.indexOf("itemSort") >= 0) {
        purl = curl;
      } else {
        purl = curl + "&itemSort=1";
      }
      console.log("详情页路径地址:" + purl);
      api.get(purl).then(
        response => {
          try {
            let components = [];
            let componentsName = [];
            let specialList = response.data;
            this.detailData = specialList;
            this.special = specialList;
            let assetType = specialList.assetType;
            let layout = specialList.layout;
            if(this.detailType!='' && this.detailType!=assetType){
                this.isInit = true;
            }else{
                this.isInit = false;
            }
            this.detailType = assetType;
            
            if (layout == "Detail_News" || layout == "Detail_Series") {
              // 判断是否为电视剧页
              document.title = "电视剧详情页";
              components.push({ ele: "seriesTop", initData: response });
              componentsName.push(specialList.assetId);
              
              if(layout == "Detail_Series"){
                this.FilmType = 1;
                components.push({ ele: "ption", initData: response });
                componentsName.push(specialList.itemList[0].itemId);
              }else {
                this.FilmType = 2;
                // 合集选择条
                document.title = "合集推荐页";
                components.push({ ele: "itemList", initData: response });
                componentsName.push(specialList.itemList[0].itemId);
              }
              this.AllEpisodes = specialList.itemList.length;
              // if (specialList.specialList[0]) {
              //   // 电视剧专题
              //   components.push({ ele: "special", initData: response });
              //   componentsName.push(specialList.specialList[0].specialId);
              // }
              if (specialList.assetList[0]) {
                // 大数据推荐
                components.push({ ele: "Movie_assetList", initData: response });
                componentsName.push(specialList.assetList[0].assetId+1);
              }
              if (specialList.actorList[0]) {
                // 演员推荐
                components.push({ ele: "Movie_actorList", initData: response });
                componentsName.push(specialList.actorList[0].actorId);
              }
              this.child = components;
              this.componentsId = componentsName;
            } else {
              this.FilmType = 0;
              this.Episodes = 1;
              //非电视剧页
              document.title = "电影详情页";
              components.push({ ele: "movie_top", initData: response });
              componentsName.push(specialList.assetId);
              
              // if (specialList.specialList[0]) {
              //   // 专题类型
              //   components.push({ ele: "special", initData: response });
              //   componentsName.push(specialList.specialList[0].specialId);
              // }
              if (specialList.assetList[0]) {
                // 电影大数据推荐
                components.push({ ele: "Movie_assetList", initData: response });
                componentsName.push(specialList.assetList[0].assetId+1);
              }
              if (specialList.actorList[0]) {
                // 演员推荐
                components.push({ ele: "Movie_actorList", initData: response });
                componentsName.push(specialList.actorList[0].actorId);
              }
              that.child = components;
              that.componentsId = componentsName;
            }
            this.nummc++;
            if (this.nummc > 1) {
              let time = new Date().valueOf();
              that.oldTime = time - that.old;
            }
            that.$nextTick(function() {
              if(this.isLiveJump){
                this.isLiveJump = false;
                this.livePv(this.special.assetName,this.special.assetId);
              }
              $(".loading").hide();
              if(this.isInit){
                status = "init";
              }
              if (layout == "Detail_Series") {
                // 如果是电视剧详情页则初始化在选集上
                if (this.isSouth) {
                  this.southFocusInit().updateStast(true, status, false);
                } else {
                  this.filterRefs().updateStast(true, status, true);
                }
              } else if (specialList.itemList[1]) {
                // 如果是单条有子集的话
                console.log("综艺详情页数据请求，键值重新分配");
                this.filterRefs().updateStast(true, status, true);
              } else {
                //  如果电影或是没有子集的情况
                console.log("电影详情页数据请求，键值重新分配");
                this.filterRefs().updateStast(true, status, true);
              };
              // if (this.child[0].initData.data.itemList[1] == undefined) {
              //   if (this.child[0].initData.data.layout != "Detail_Series" && this.child[0].initData.data.layout != "Detail_Movie") {
              //     setTimeout(() => {
              //       console.log("其他资产类型默第一个组件");
              //       this.filterRefs().updateStast(true, status, true);
              //     }, 0);
              //   }
              // }
            });
          } catch (error) {
            Toast("该资产已下线,3s后自动返回'");
            try {
              // 查询到资产信息异常需要把本地历史记录和收藏清除
              const assetId = c.getQueryString(this.jsonUrl, "assetId");
              c.removeCollect(assetId); // 移除收藏
              if (this.isSouth) {
                // 豫南
                c.removeHistoryS(assetId);
              } else {
                yh.removeHistorys(assetId); // 移除历史
              }
            } catch (error) {
              console.log("删除本地历史记录异常");
            }
            setTimeout(() => {
              this.$router.go(-1);
            }, 3000);
          }
        },
        response => {
          console.log("get detail time error");
          $(".openImage").hide();
          this.timeOutTimess++;
          if (this.timeOutTimess > 1) {
            if (status == "updateUrl") {
              this.filterRefs().updateStast(true, status, false);
            } else {
              $(".loading").show();
              console.log("detail request time out,three times load end");
              Toast("请求超时，3s后自动返回首页");
              setTimeout(() => {
                if (yh.isLive()) {
                       yh.finishOpen(); //            销毁详情页
                }else{
                    if (this.isSouth) {
                      this.$router.push({ name: "south" });
                    } else {
                      this.$router.push({ name: "index" });
                    }
                }
                
              }, 3000);
            }
          } else {
            this.init(this.waitting);
            
          }
        }
      );
    },
    load() {
      try {
        if (this.nummc < 2) {
          this.oldTime =
            performance.timing.domContentLoadedEventEnd -
            performance.timing.navigationStart;
          this.pageId = this.special.assetId;
          c.setTrackPath(this.pageId, 3);
          let traceId = c.getTrackPath();
          let traceType = c.getTrackPathType();
          let catId = c.getParentPageId();
          let catCode = c.getParentPageType();
          this.oldTimes = new Date().valueOf();
          bi.loadPage(
            traceId,
            traceType,
            31,
            this.pageId,
            catCode,
            catId,
            this.oldTime
          );
        }
      } catch (error) {
        console.log("采集异常");
      }
      try {
        // 加载yh埋点
        const BiJson = {};
        BiJson.cat_id = this.category[this.navpos].catId; //所在栏目id
        BiJson.trace_id = c.getTrackPath(); //当前访问页面id路径、轨迹，以“|”分割
        BiJson.trace_type = c.getTrackPathType(); //当前访问页面路径、轨迹，以“|”分割的页面类型
        BiJson.parent_page_type = c.getParentPageType();
        BiJson.parent_page_id = c.getParentPageId(); //父级页面id
        BiJson.special_type = this.$route.params.bp.special_type || "";
        BiJson.recommend_place = this.category[this.navpos].catName; //未知
        BiJson.special_id = this.$route.params.bp.special_id || "";
        BiJson.cat_name = this.category[this.navpos].catName; //所在栏目名称
        BiJson.enterType = this.$route.params.bp.enterType || 5; //进入点播方式 哪些是点播资源/嗨看资源？
        BiJson.filmType = "1"; //影片类型(0:嗨看 1: 点播) 我们都是点播
        BiJson.assetId = this.special.assetId;
        BiJson.assetName = this.special.assetName;
        BiJson.type = "0" + c.getParentPageType();
        BiJson.typeId =
        this.category[this.navpos].catId == "202652" ? "202652" : "0";
        // BiJson.pagePath = c.getTrackPathType().replace(/\|/gi, "_");
        // BiJson.srcPagePath = c
        //   .getTrackPathType()
        //   .slice(0, c.getTrackPathType().length - 2)
        //   .replace(/\|/gi, "_");
        yh.setBiJson(JSON.stringify(BiJson));
      } catch (error) {
        console.log("BI_BEAN.setBiJson采集异常");
      }
    },
    closPages() {
      let time = new Date().valueOf();
      let times = time - this.oldTimes;
      this.pageId = this.special.assetId;
      let traceId = c.getTrackPath();
      let traceType = c.getTrackPathType();
      bi.closPage(traceId, traceType, 31, this.pageId, times);
    },
    filterRefs() {
      // 筛选头部组件
      let refs = this.$refs.childDo;
      let component = null;
      for (let index = 0; index < refs.length; index++) {
        const element = refs[index];
        if (element.name == "movieTop") {
          component = element;
          break;
        } else if (element.name == "seriesTop") {
          component = element;
          break;
        }
      }
      return component;
    },getSelectionRef(){ // 获取选集条组件
      let refs = this.$refs.childDo;
      let component = null;
      for (let index = 0; index < refs.length; index++) {
        const element = refs[index];
        if (element.name == "ption") {
          component = element;
          break;
        }
      }
      return component;
    },
    southFocusInit() {
      // 筛选豫南切换级数组件，用于初始化豫南焦点
      let refs = this.$refs.childDo;
      let component = null;
      for (let i = 0; i < refs.length; i++) {
        const element = refs[i];
        if(element.name == "ption"){
          component = element;
        }
      }
      return component;
    },livePv(name,assetId){
      // 星红桉埋点
      let SrcPagePath = "";
      let PageName = name;
      let AsssetID = assetId;
      let Id = "0";
      let TYPE = "";
      xha.pageLoad("直播关联推荐",SrcPagePath,PageName,AsssetID,Id,TYPE);
    }
  },
  computed: {
    ...mapGetters(["category", "behindParams", "isHTTPS", "navpos", "isSouth","episode"])
  },
  components: {
    movie_top,
    Movie_actorList,
    Movie_assetList,
    itemList,
    ption,
    seriesTop,
    description,
    special,
    order
  },
  watch: {
    special() {
      this.$nextTick(function() {
        this.load();
        this.isEnd = false;
        this.pageId = this.special.assetId;
        this.assetInfo = {
          contentCode: this.pageId,
          contentName: this.special.assetName,
          assetImg: this.special.assetImg,
          assetSp: this.special.providerId,
        };
        this.getAssentsPPV(); // 获取ppv
        console.log("=====" + JSON.stringify(this.assetInfo));
      });
    }
  },
  beforeRouteLeave(to, from, next) {
    try {
      this.isdestory = true;
      this.closPages();
      this.$destroy();
    } catch (error) {
    } finally {
      next();
    }
  },
  beforeRouteEnter(to, from, next) {
    if (from.name != null) {
      localStorage.setItem("detailback", from.name);
    }
    window.sessionStorage.removeItem("srcPagePath");
    window.sessionStorage.setItem("srcPagePath",location.origin+'/henan'+from.path);
    next();
  }
};
</script>



